// src/components/Navbar.tsx
import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';

const Navbar: React.FC = () => {
  const [isAuthenticated, setIsAuthenticated] = useState<boolean>(false);

  useEffect(() => {
    const token = localStorage.getItem('googleToken');
    setIsAuthenticated(!!token);
  }, []);

  const handleAuthClick = () => {
    if (isAuthenticated) {
      // Logout: remove token from localStorage and update state
      localStorage.removeItem('googleToken');
      setIsAuthenticated(false);
      window.location.href = '/';
    } else {
      // Sign in: redirect to the Netlify auth function
      window.location.href = 'http://localhost:8888/auth/';
    }
  };

  return (
    <nav style={navbarStyle}>
      <div>
        <Link to="/" style={linkStyle}>
          Giver
        </Link>
        <Link to="/doer" style={linkStyle}>
          Doer
        </Link>
        {/* New Report button/link */}
        <Link to="/report" style={linkStyle}>
          Report
        </Link>
      </div>
      <div>
        <button style={buttonStyle} onClick={handleAuthClick}>
          {isAuthenticated ? 'Logout' : 'Sign in with Google'}
        </button>
      </div>
    </nav>
  );
};

const navbarStyle: React.CSSProperties = {
  background: '#333',
  color: '#fff',
  padding: '1rem',
  display: 'flex',
  justifyContent: 'space-between',
  alignItems: 'center',
};

const linkStyle: React.CSSProperties = {
  color: '#fff',
  marginRight: '1rem',
  textDecoration: 'none',
};

const buttonStyle: React.CSSProperties = {
  background: '#fff',
  color: '#333',
  border: 'none',
  padding: '0.5rem 1rem',
  cursor: 'pointer',
  borderRadius: '4px',
};

export default Navbar;
