// src/pages/doer/FormSchemaRenderer.tsx
import React, { useEffect, useState } from 'react'
import { craftySupabase } from '../../craftySupabaseClient'
import "../../pages/doer/FormSchemaRenderer.css"

interface Props {
  formUrl: string
  onSubmit: (values: Record<string, any>, fields: string[]) => void
}

type FieldType =
  | 'text'
  | 'email'
  | 'number'
  | 'textarea'
  | 'dropdown'
  | 'checkbox'
  | 'radio'
  | 'file'
  | 'video'
  | 'audio'

interface SimpleField {
  type: FieldType
  title: string
  placeholder?: string
  required: boolean
  options?: { value: string; label: string }[]
}

interface SimpleSchema {
  title: string
  properties: Record<string, SimpleField>
}

export const FormSchemaRenderer: React.FC<Props> = ({ formUrl, onSubmit }) => {
  const [rawSchema, setRawSchema] = useState<any>(null)
  const [effectiveRawSchema, setEffectiveRawSchema] = useState<any>(null)
  const [schema, setSchema] = useState<SimpleSchema | null>(null)
  const [loading, setLoading] = useState(false)
  const [formData, setFormData] = useState<Record<string, any>>({})
  const [dropdownOptions, setDropdownOptions] = useState<Record<string, string[]>>({})

  const extractId = (url: string): string | null => {
    try {
      const clean = url.split(/[?#]/)[0].replace(/\/+$/, '')
      const last = clean.split('/').pop()!
      return /^[0-9a-f]{8}-[0-9a-f]{4}-[0-9a-f]{4}-[0-9a-f]{4}-[0-9a-f]{12}$/i.test(last)
        ? last
        : null
    } catch {
      return null
    }
  }

  const normalize = (raw: any): SimpleSchema => {
    const requiredFields = Array.isArray(raw.required) ? raw.required : []
    const props = raw.properties as Record<string, any>
    const simpleProps: Record<string, SimpleField> = {}

    for (const [key, p] of Object.entries(props) as [string, any][]) {
      const isReq = requiredFields.includes(key)
      const title = p.title || key
      const placeholder = p.description ? `Enter ${title}` : undefined

      if (Array.isArray(p.enum)) {
        simpleProps[key] = {
          type: 'dropdown',
          title,
          required: isReq,
          options: p.enum.map((v: any) => ({ value: String(v), label: String(v) })),
        }
      } else if (p.type === 'boolean') {
        simpleProps[key] = {
          type: 'checkbox',
          title,
          required: isReq,
          options: [{ value: 'true', label: title }],
        }
      } else if (p.type === 'array' && p.items?.enum) {
        simpleProps[key] = {
          type: 'checkbox',
          title,
          required: isReq,
          options: p.items.enum.map((v: any) => ({ value: String(v), label: String(v) })),
        }
      } else if (p.type === 'integer' || p.type === 'number') {
        simpleProps[key] = {
          type: 'number',
          title,
          placeholder,
          required: isReq,
        }
      } else if (p.type === 'string') {
        if (p.format === 'email') {
          simpleProps[key] = { type: 'email', title, placeholder, required: isReq }
        } else if (p.maxLength && p.maxLength > 200) {
          simpleProps[key] = { type: 'textarea', title, placeholder, required: isReq }
        } else {
          simpleProps[key] = { type: 'text', title, placeholder, required: isReq }
        }
      } else {
        simpleProps[key] = { type: 'text', title, placeholder, required: isReq }
      }
    }

    return { title: raw.title || '', properties: simpleProps }
  }

  useEffect(() => {
    const id = extractId(formUrl)
    if (!id) return console.warn('Invalid form URL:', formUrl)
    setLoading(true)
    ;(async () => {
      const { data, error } = await craftySupabase
        .from('form_schemas')
        .select('schema')
        .eq('id', id)
        .single()
      if (error) console.error(error)
      else if (data?.schema) setRawSchema(data.schema)
      setLoading(false)
    })()
  }, [formUrl])

  useEffect(() => {
    if (!rawSchema) return
    const baseProps = { ...rawSchema.properties }
    const baseRequired = Array.isArray(rawSchema.required)
      ? [...rawSchema.required]
      : []

    ;(rawSchema.allOf || []).forEach((clause: any) => {
      const condProps = clause.if?.properties
      if (!condProps) return
      const [fieldName] = Object.keys(condProps)
      const expectedValue = condProps[fieldName]?.const
      if (expectedValue === undefined) return
      if (formData[fieldName] === expectedValue) {
        Object.assign(baseProps, clause.then.properties)
        if (Array.isArray(clause.then.required)) {
          clause.then.required.forEach((r: string) => {
            if (!baseRequired.includes(r)) baseRequired.push(r)
          })
        }
      }
    })

    setEffectiveRawSchema({
      ...rawSchema,
      properties: baseProps,
      required: baseRequired,
      allOf: []
    })
  }, [rawSchema, JSON.stringify(formData)])

  useEffect(() => {
    if (!effectiveRawSchema) return
    setSchema(normalize(effectiveRawSchema))
  }, [effectiveRawSchema])

  const handleInputChange = (
    key: string,
    value: any,
    mode: 'checkbox' | 'default' = 'default'
  ) => {
    setFormData(prev => {
      if (mode === 'checkbox') {
        const existing: string[] = prev[key] || []
        const next = existing.includes(value)
          ? existing.filter(v => v !== value)
          : [...existing, value]
        return { ...prev, [key]: next }
      }
      return { ...prev, [key]: value }
    })
  }

  const openRecordingModal = (key: string, mode: FieldType) => {
    // implement recording logic
  }

  const handleFileUpload = (key: string, file: File) => {
    const url = URL.createObjectURL(file)
    setFormData(d => ({ ...d, [key]: url }))
  }

  const handleDependentDropdown = (
    key: string,
    value: string,
    depKey: string,
    depOpts: string[]
  ) => {
    setDropdownOptions(o => ({ ...o, [depKey]: depOpts }))
  }

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault()
    if (!schema) return
    const fields = Object.keys(schema.properties)
    onSubmit(formData, fields)
  }

  if (loading) return <p>Loading form</p>
  if (!schema) return <p>No form schema found.</p>

  const renderField = (field: SimpleField, key: string) => {
    const label = field.title
    const placeholder = field.placeholder || `Enter ${label}`
    const optionsList =
      field.options ??
      (dropdownOptions[key] || []).map(v => ({ value: v, label: v }))

    switch (field.type) {
      case 'dropdown':
        return (
          <div key={key}>
            <label>{label}</label>
            <select
              value={formData[key] ?? ''}
              required={field.required}
              onChange={e => handleInputChange(key, e.target.value)}
            >
              <option value="" disabled>{placeholder}</option>
              {optionsList.map(opt => (
                <option key={opt.value} value={opt.value}>
                  {opt.label}
                </option>
              ))}
            </select>
          </div>
        )
      case 'checkbox':
        return (
          <div key={key}>
            <label>{label}</label>
            {optionsList.map(opt => (
              <div key={opt.value}>
                <input
                  type="checkbox"
                  checked={(formData[key] || []).includes(opt.value)}
                  onChange={() => handleInputChange(key, opt.value, 'checkbox')}
                />
                <label>{opt.label}</label>
              </div>
            ))}
          </div>
        )
      case 'radio':
        return (
          <div key={key}>
            <label>{label}</label>
            {optionsList.map(opt => (
              <div key={opt.value}>
                <input
                  type="radio"
                  name={key}
                  value={opt.value}
                  checked={formData[key] === opt.value}
                  required={field.required}
                  onChange={e => handleInputChange(key, e.target.value)}
                />
                <label>{opt.label}</label>
              </div>
            ))}
          </div>
        )
      case 'number':
      case 'text':
      case 'email':
        return (
          <div key={key}>
            <label>{label}</label>
            <input
              type={field.type}
              placeholder={placeholder}
              value={formData[key] ?? ''}
              required={field.required}
              onChange={e => handleInputChange(key, e.target.value)}
            />
          </div>
        )
      case 'textarea':
        return (
          <div key={key}>
            <label>{label}</label>
            <textarea
              placeholder={placeholder}
              value={formData[key] ?? ''}
              required={field.required}
              onChange={e => handleInputChange(key, e.target.value)}
            />
          </div>
        )
      case 'file':
        return (
          <div key={key}>
            <label>{label}</label>
            <input
              type="file"
              required={field.required}
              onChange={e => e.target.files?.[0] && handleFileUpload(key, e.target.files![0])}
            />
          </div>
        )
      case 'video':
      case 'audio':
        return (
          <div key={key}>
            <label>{label}</label>
            <button type="button" onClick={() => openRecordingModal(key, field.type)}>
              Record {field.type}
            </button>
            {formData[key] && (
              <div style={{ marginTop: 8 }}>
                {field.type === 'video' ? (
                  <video controls src={formData[key]} width={250} />
                ) : (
                  <audio controls src={formData[key]} />
                )}
              </div>
            )}
          </div>
        )
      default:
        return null
    }
  }

  return (
    <form onSubmit={handleSubmit}>
      <h3>{schema.title}</h3>
      {Object.entries(schema.properties).map(([key, field]) =>
        renderField(field, key)
      )}
      <button type="submit">Submit</button>
    </form>
  )
}
