// src/pages/SyncResponses.tsx
import React, { useState } from 'react';

const SyncResponses: React.FC = () => {
  const [formId, setFormId] = useState('');
  const [processId, setProcessId] = useState('');
  const [status, setStatus] = useState('');

  const handleSync = async () => {
    if (!formId || !processId) {
      alert('Need formId and processId');
      return;
    }

    try {
      const resp = await fetch('http://localhost:5000/api/syncFormResponses', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ formId, processId }),
      });
      if (!resp.ok) {
        throw new Error(`Server responded with ${resp.status}`);
      }
      const data = await resp.json();
      setStatus(`Synced. Inserted ${data.inserted} out of ${data.totalResponses}`);
    } catch (err) {
      console.error(err);
      setStatus(`Error: ${(err as Error).message}`);
    }
  };

  return (
    <div style={{ padding: '1rem' }}>
      <h2>Sync Google Form Responses</h2>
      <div>
        <label>Form ID:</label>
        <input
          type="text"
          value={formId}
          onChange={(e) => setFormId(e.target.value)}
        />
      </div>
      <div>
        <label>Process ID:</label>
        <input
          type="text"
          value={processId}
          onChange={(e) => setProcessId(e.target.value)}
        />
      </div>
      <button onClick={handleSync}>Sync</button>
      <p>{status}</p>
    </div>
  );
};

export default SyncResponses;
