
// helper to prompt/store Wasender API key in localStorage
export function getWasenderToken(): string | null {
  let token = localStorage.getItem('WASENDER_TOKEN');
  if (!token) {
    token = window.prompt('Please enter your WhatsApp API key for Wasender:');
    if (token) {
      localStorage.setItem('WASENDER_TOKEN', token);
    } else {
      alert('WhatsApp API key is required to send alerts.');
      return null;
    }
  }
  return token;
}