/**
 * Compute the same Process Flow Overview columns for `step`
 * against the sorted list `all`.
 */
function computeOverviewMetrics(
  step: StepRecord,
  all: StepRecord[]
): Record<string, any> {
  // Ensure steps are sorted by sequence_id
  const sorted = [...all].sort((a, b) => a.sequence_id - b.sequence_id);

  // Find index of current step in sorted array
  const idx = sorted.findIndex(s => s.sequence_id === step.sequence_id);

  // Total steps count
  const total = sorted.length;

  // Direct children in a linear sequence = next element only
  const directChildren = idx >= 0 && idx < total - 1 ? 1 : 0;

  // Descendants count = all following steps
  const descendants = idx >= 0 ? total - idx - 1 : 0;

  // Build full chain by stepping backwards through sorted
  const chainArr: string[] = [];
  for (let i = idx; i >= 0; i--) {
    chainArr.push(sorted[i].name);
  }
  const chainOfReference = chainArr.join('  ');
  const chainOfReferenceWithCycles = chainOfReference;

  // Ancestor count is length of chain minus 1
  const ancestors = chainArr.length > 0 ? chainArr.length - 1 : 0;

  // Root is the very first step
  const rootName = sorted[0]?.name || '';

  // Days since immediate previous (referrer)
  const daysSinceReferrerJoined = idx > 0
    ? Math.floor(
        (new Date(step.created_at).getTime() - new Date(sorted[idx - 1].created_at).getTime()) /
        (1000 * 60 * 60 * 24)
      )
    : 0;

  // Days from root
  const daysFromRoot = idx >= 0
    ? Math.floor(
        (new Date(step.created_at).getTime() - new Date(sorted[0].created_at).getTime()) /
        (1000 * 60 * 60 * 24)
      )
    : 0;

  const inWords = `${step.name} has ${directChildren} direct children and ${descendants} total descendants. It is ${ancestors} step(s) from ${rootName}.`;

  return {
    step: step.name,
    dependentOn: idx > 0 ? sorted[idx - 1].name : null,
    date: step.created_at,
    directChildren,
    descendants,
    ancestors,
    root: rootName,
    chainOfReference,
    chainOfReferenceWithCycles,
    daysSinceReferrerJoined,
    daysFromRoot,
    inWords,
    getRoot: rootName,
    isRoot: idx === 0
  };
}


