/**
 * Called when, after choosing split, the user picks a Process + Step.
 * We look up the proc & step names, build a target string,
 * and write it back into the same mapping entry.
 */
const handleOptionChooseStep = (
  fieldKey: string,
  optValue: string,
  stepId: string
) => {
  // 1 read the process ID they picked earlier
  const procId = optionSplitProcessByField[fieldKey]?.[optValue];
  if (!procId) return;

  // 2 find the Process and Step objects
  const proc = processes.find(p => p.id === procId);
  const step = proc?.steps.find(s => s.id === stepId);
  if (!proc || !step) return;

  // 3 build the humanreadable target
  const target = `Step: ${step.name} in ${proc.name}`;




  // 4 update the exact mapping entry
  setOptionActionMapping(prev => ({
    ...prev,
    [fieldKey]: {
      ...(prev[fieldKey] || {}),
      [optValue]: { action: 'split', target }
    }
  }));

  // 5 also store the chosen stepId so your UI stays in sync
  setOptionSplitStepByField(prev => ({
    ...prev,
    [fieldKey]: {
      ...(prev[fieldKey] || {}),
      [optValue]: stepId
    }
  }));
};


