  /**
   * Return a string like "2025-05-21 12:08:00" for the current time in Asia/Kolkata.
   */
  function getISTTimestamp(date?: Date): string {
    const base = date ?? new Date();
    const utcMs      = base.getTime() + base.getTimezoneOffset() * 60 * 1000;
    const istOffset  = 5.5 * 60 * 60 * 1000;
    const istDate    = new Date(utcMs + istOffset);
    const pad = (n: number) => String(n).padStart(2, '0');
    const Y   = istDate.getFullYear();
    const M   = pad(istDate.getMonth() + 1);
    const D   = pad(istDate.getDate());
    const h   = pad(istDate.getHours());
    const m   = pad(istDate.getMinutes());
    const s   = pad(istDate.getSeconds());
    return `${Y}-${M}-${D} ${h}:${m}:${s}`;
  }

