  const currentProcess = processes.find(p => p.id === flowchartId);
  const prior = currentProcess
    ? currentProcess.steps
        .filter((s: { sequence_id: number }) => s.sequence_id < sequenceId)
        .sort(
          (a: { sequence_id: number }, b: { sequence_id: number }) =>
            b.sequence_id - a.sequence_id
        )
    : [];
  const dependencyStep = prior[0];  // the immediate predecessor, if any

