  //  Save (create/update) the step, including the appropriate responsibility 
const handleCreateStep = async (draft = false) => {
  if (!flowchartId) {
    alert('Missing flowchartId');
    return;
  }

  // 1 Auth check
  const {
    data: { session },
  } = await supabase.auth.getSession();
  if (!session?.user) {
    alert('Not authenticated');
    return;
  }
  const userId = session.user.id;

  // 2 Autonext for statuses
  const statusesWithAutoNext = statuses.map(st =>
    st.nextStep
      ? { ...st, nextStepId: stepId + 1, nextSequenceId: sequenceId + 1 }
      : st
  );

  // 3 Build payload
  const payload: any = {
    process_id:            flowchartId,
    step_id:               stepId,
    sequence_id:           sequenceId,
    name:                  stepName || null,
    tat_value:             tat ? parseInt(tat, 10) : null,
    tat_unit:              tatUnit,
    dependencies:          dependency ? [dependency] : [],
    responsibility:        responsibility || null,
    is_draft:              draft,
    user_id:               userId,
    step_type:             stepType,

    started_at:            startMode === 'immediate' ? getISTTimestamp() : null,
    scheduled_at:          startMode === 'scheduled'
                              ? getISTTimestamp(new Date(scheduledAt))
                              : null,

    statuses:              stepType === 'status'    ? statusesWithAutoNext   : undefined,
    checklists:            stepType === 'checklist' ? checklists             : undefined,
    checklistActionMapping:
                          stepType === 'checklist' ? checklistActionMapping : undefined,

    form_url:              stepType === 'crafty'    ? craftyUrl.trim()       : undefined,
    crafty_action_mapping:
                          stepType === 'crafty'    ? craftyActionMapping    : undefined,

    // always include your JSONB fields
    field_action_mapping:  fieldActionMapping,
    option_action_mapping: optionActionMapping,

alerts: alertsList.map(al => ({
    statusName: al.statusName,
    type: al.type,
    email: al.email,
    template: al.template,
    buttons: al.buttons?.map(b => ({ title: b.title, reply: b.reply })) || [],
    fieldKey: al.fieldKey || null
  })),
    reminders,
    is_timer_enabled: timerEnabled,
  };


    //  NEW: compute & attach process_flow_overview 
  const newStepRecord: StepRecord = {
    id: 'temp',
    step_id: stepId,
    sequence_id: sequenceId,
    name: stepName,
    created_at: getISTTimestamp(),
    dependencies: dependency ? [dependency] : []
  };
  const overview = computeOverviewMetrics(
    newStepRecord,
    [...allSteps, newStepRecord].sort((a, b) => a.sequence_id - b.sequence_id)
  );

  console.log(' STEP PAYLOAD:', payload);

  // 4 Check for existing row
  const { data: existing, error: fetchErr } = await supabase
    .from('steps')
    .select('id')
    .eq('process_id', flowchartId)
    .eq('step_id', stepId)
    .eq('sequence_id', sequenceId)
    .single();

  if (fetchErr && fetchErr.code !== 'PGRST116') {
    // PGRST116 = no rows found (treat as nonerror)
    alert(`Error checking existing step: ${fetchErr.message}`);
    return;
  }

  // 5 Insert or update
  let res;
  if (existing?.id) {
    res = await supabase
      .from('steps')
      .update(payload)
      .eq('id', existing.id)
      .select('*');
  } else {
    res = await supabase
      .from('steps')
      .insert(payload)
      .select('*');
  }

  console.log(' DATABASE RESULT:', res);

  if (res.error) {
    alert(`Error saving step: ${res.error.message}`);
    return;
  }


    //  NEW: Log step creation or update with full context
  const savedStep = Array.isArray(res.data) ? res.data[0] : res.data;
  await supabase
    .from('logs')
    .insert({
      event_type: existing?.id ? 'step_update' : 'step_create',
      details: JSON.stringify({
        processId:        flowchartId,
        stepId:           stepId,
        sequenceId:       sequenceId,
        dbRowId:          savedStep?.id,
        userId:           session.user.id,
        responsibility:   responsibility || null,
        isDraft:          draft,
        tatValue:         payload.tat_value,
        tatUnit:          payload.tat_unit,
        stepType:         payload.step_type,
        timestamp:        getISTTimestamp()
      }),
      created_at: getISTTimestamp()
    });




  //  NEW: store the overview in its own table 
  await supabase
    .from('step_flow_overviews')
    .upsert({
      process_id:     flowchartId!,
      step_id:        stepId,
      sequence_id:    sequenceId,
      overview
    });

  alert('Step saved successfully, and overview stored separately!');
  navigate(-1);
};


