/** The shape of the "process" object from embedded select. */
interface ProcessData {
  name: string;
  ghost_mode?: boolean;
}

/** The shape of each row in "steps". */
interface Step {
  id: string;
  process_id: string;
  name: string;
  // tat_value?: number;
  // tat_unit?: string;
  sequence_id?: number;
  // created_at?: string;
  process?: ProcessData | null;
  // New boolean for escalations
  is_escalated?: boolean;
  // Default statuses stored on the step (if any)
  statuses?: StatusData[];

  // The email of whoevers responsible for this step
  responsibility?: string;
  is_timer_enabled?: boolean;
  tat_value?: number;      // ensure you have TAT value & unit
  tat_unit?: 'mins'|'hrs'|'days'|'weeks';
  created_at?: string;
}

/** Each status (template) */
interface StatusData {
  statusName: string;
  statusType: string;
  linkNext: string;
  inputType: string;  // "text" | "checkbox" | "radio" | "dropdown" | "button"
  options: string;    // comma-separated for checkbox/radio/dropdown
  selectedValue?: string;
  selectedOptions?: string[];
}

/** The shape of each row in "form_submissions". */
interface FormSubmission {
  id: string;
  process_id: string;
  created_at?: string;
  fields?: string;
  responses?: string;
  data?: Record<string, any>; 
}

/** The shape of a submission_statuses record */
interface SubmissionStatusRecord {
  submission_id: string;
  statuses: StatusData[];
}

/** Virtual item: pairs a Step + a FormSubmission */
interface StepSubmissionItem {
  id: string; // unique => stepId + '_' + submissionId
  step: Step;
  submission: FormSubmission;
  statuses: StatusData[];
  readOnly?: boolean;
  prevResponsible?: string;
   formData?: Record<string, any>;
   selectedFields?: string[];   
   startedAt?: string;
}


//  Add this new component just above your DoerStepList 
interface CountdownTimerProps {
  enabled?: boolean;
  createdAt?: string;
  durationValue?: number;
  durationUnit?: 'mins'|'hrs'|'days'|'weeks';
}
const CountdownTimer: React.FC<CountdownTimerProps> = ({
  enabled,
  createdAt,
  durationValue,
  durationUnit
}) => {
  const [now, setNow] = useState(Date.now());
  useEffect(() => {
    if (!enabled) return;
    const iv = setInterval(() => setNow(Date.now()), 1000);
    return () => clearInterval(iv);
  }, [enabled]);

  if (!enabled || !createdAt) return null;

  const multiplier = {
    mins:   60,
    hrs:    3600,
    days:   86400,
    weeks:  604800
  }[durationUnit || 'hrs'];
 const durationMs = (durationValue || 0) * multiplier * 1000;
 const end        = new Date(createdAt).getTime() + durationMs;
 const remaining  = Math.max(0, end - now);

const h = String(Math.floor(remaining / 3600000)).padStart(2, '0');
  const m = String(Math.floor((remaining % 3600000) / 60000)).padStart(2, '0');
  const s = String(Math.floor((remaining % 60000) / 1000)).padStart(2, '0');

  return (
    <div style={{ fontSize: '0.9rem', marginLeft: '0.5rem', color: '#c00' }}>
      Time left: {h}:{m}:{s}
    </div>
  );
};


/** Grouping modes. */
type GroupMode = 'none' | 'process' | 'dataPoints' | 'randomTasks';
interface Group {
  groupKey: string;
  items: StepSubmissionItem[];
}

/** Sort modes. */
type SortMode = 'none' | 'tatAsc' | 'tatDesc' | 'seqAsc' | 'seqDesc';

/** Filter modes. */
type FilterMode = 'all' | 'lowTat' | 'highTat';

/**
 * Helper function to convert a TAT value to hours.
 * - mins: value/60
 * - hrs: value
 * - days: value*24
 * - weeks: value*168 (7*24)
 */
const convertToHours = (value: number, unit: string = 'hrs'): number => {
  switch (unit) {
    case 'mins':
      return value / 60;
    case 'days':
      return value * 24;
    case 'weeks':
      return value * 168;
    case 'hrs':
    default:
      return value;
  }
};



