// src/pages/ReportPage.tsx
import React, { useEffect, useState } from "react";
import { supabase } from "../supabaseClient";
import { FiChevronDown, FiChevronRight } from "react-icons/fi";
import type { IconBaseProps } from "react-icons";

const ChevronDown = FiChevronDown as React.ComponentType<IconBaseProps>;
const ChevronRight = FiChevronRight as React.ComponentType<IconBaseProps>;

// Hook to detect mobile
function useIsMobile(breakpoint = 600) {
  const [isMobile, setIsMobile] = useState(
    window.innerWidth < breakpoint
  );
  useEffect(() => {
    const onResize = () =>
      setIsMobile(window.innerWidth < breakpoint);
    window.addEventListener("resize", onResize);
    return () => window.removeEventListener("resize", onResize);
  }, [breakpoint]);
  return isMobile;
}

interface Submission {
  id: string;
  data?: Record<string, any>;
  created_at?: string;
}
interface Step {
  id: string;
  name: string;
  responsibility?: string;
}
interface ProcessWithDetails {
  id: string;
  name: string;
  steps: Step[];
  form_submissions: Submission[];
}
interface StatusRecord {
  submission_id: string;
  step_id: string;
  started_at: string | null;
  completed_at: string | null;
}

export default function ReportPage() {
  const isMobile = useIsMobile();
  const [processes, setProcesses] = useState<ProcessWithDetails[]>([]);
  const [stepStatuses, setStepStatuses] = useState<
    Record<string, Record<string, StatusRecord>>
  >({});
  const [expanded, setExpanded] = useState<Record<string, boolean>>({});
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    loadData();
  }, []);

  async function loadData() {
    setLoading(true);
    const {
      data: { session },
    } = await supabase.auth.getSession();
    if (!session?.user) {
      setLoading(false);
      return;
    }
    const { data: procs, error: pErr } = await supabase
      .from("processes")
.select(`
     id,
   name,
   steps ( id, name, responsibility ),
   form_submissions ( id, data, created_at )
 `)
      .eq("user_id", session.user.id)
      .order("created_at", { ascending: false });

    if (pErr || !procs) {
      console.error(pErr);
      setLoading(false);
      return;
    }
    setProcesses(procs);

    const allIds = procs.flatMap((p) => p.form_submissions.map((s) => s.id));
    if (allIds.length) {
      const { data: stats } = await supabase
        .from("submission_step_statuses")
        .select("submission_id,step_id,started_at,completed_at")
        .in("submission_id", allIds);
      const map: any = {};
      stats?.forEach((r) => {
        map[r.submission_id] ||= {};
        map[r.submission_id][r.step_id] = r;
      });
      setStepStatuses(map);
    }
    setLoading(false);
  }

  const toggle = (id: string) =>
    setExpanded((ex) => ({ ...ex, [id]: !ex[id] }));

  if (loading) return <p style={styles.loading}>Loading</p>;

  const formatTTL = (ms: number) => {
    const totalMin = Math.round(ms / 60000);
    if (totalMin < 60) return `${totalMin}m`;
    const totalHr = Math.floor(totalMin / 60);
    if (totalHr < 24) return `${totalHr}h`;
    return `${Math.floor(totalHr / 24)}d`;
  };

  const items = processes.flatMap((proc) =>
    proc.form_submissions.map((sub) => ({ proc, sub }))
  );

  return (
    <div style={styles.container}>
      <h2 style={styles.title}>By Data Points</h2>
      <div style={styles.timelineContainer}>
        {items.map(({ proc, sub }) => {
          const when = sub.created_at
            ? new Date(sub.created_at).toLocaleString()
            : "";
          const isOpen = !!expanded[sub.id];
          const stMap = stepStatuses[sub.id] || {};

          // TTL calculation
          const starts = proc.steps
            .map((st) => stMap[st.id]?.started_at)
            .filter(Boolean)
            .map((t) => new Date(t as string).getTime());
          const completes = proc.steps
            .map((st) => stMap[st.id]?.completed_at)
            .filter(Boolean)
            .map((t) => new Date(t as string).getTime());
          let ttlLabel = "";
          if (starts.length && completes.length) {
            ttlLabel = formatTTL(
              Math.max(...completes) - Math.min(...starts)
            );
          }

          const formKeys = Object.keys(sub.data || {});
          const headerKeys = formKeys.slice(0, 3);

          return (
            <div
              key={sub.id}
              style={{
                ...styles.itemRow,
                flexDirection: isMobile ? "column" : "row",
                alignItems: isMobile ? "stretch" : "flex-start",
              }}
            >
              {/* Timestamp */}
              <div
                style={{
                  ...styles.timestamp,
                  width: isMobile ? "100%" : 120,
                  textAlign: isMobile ? "left" : "right",
                  marginBottom: isMobile ? 8 : 0,
                }}
              >
                {when}
              </div>

                      {/* Line + Dot (only on desktop) */}
          {!isMobile && (
            <div
              style={{
                ...styles.lineContainer,
                marginBottom: 0,          // no extra bottommargin on desktop
              }}
            >
              <div style={styles.verticalLine} />
              <div style={styles.dot} />
            </div>
          )}

              {/* Card */}
              <div style={styles.card}>
                {/* Header */}
                <div
                  style={{
                    ...styles.cardHeader,
                    padding: isMobile ? 8 : 12,
                  }}
                  onClick={() => toggle(sub.id)}
                >
                  {isOpen ? <ChevronDown size={16} /> : <ChevronRight size={16} />}
                  {headerKeys.map((k) => (
                    <span
                      key={k}
                      style={{
                        ...styles.headerField,
                        marginLeft: isMobile ? 8 : 12,
                        fontSize: isMobile ? 14 : 16,
                      }}
                    >
                      <strong>{k}:</strong> {sub.data?.[k] ?? ""}
                    </span>
                  ))}
                  <span
                    style={{
                      ...styles.ttl,
                      marginLeft: "auto",
                      fontSize: isMobile ? 14 : 16,
                    }}
                  >
                    {ttlLabel}
                  </span>
                </div>

                {/* Expanded */}
                {isOpen && (
                  <div style={styles.expandedSection}>
                    {/* Scrollable Form Data */}
                    <div style={styles.tableWrapper}>
                      <table style={styles.table}>
                        <thead>
                          <tr>
                            {formKeys.map((k) => (
                              <th key={k} style={styles.th}>
                                {k}
                              </th>
                            ))}
                          </tr>
                        </thead>
                        <tbody>
                          <tr>
                            {formKeys.map((k) => (
                              <td key={k} style={styles.td}>
                                {String(sub.data?.[k] ?? "")}
                              </td>
                            ))}
                          </tr>
                        </tbody>
                      </table>
                    </div>

                    {/* Scrollable Steps/Status */}
                    <div style={styles.tableWrapper}>
                      <table style={styles.table}>
                        <thead>
                          <tr>
                            <th style={styles.th}>Process</th>
                            <th style={styles.th}>Step</th>
                            <th style={styles.th}>Goes To</th>
                            <th style={styles.th}>Status</th>
                          </tr>
                        </thead>
                        <tbody>
                      {proc.steps.map((step, idx) => {
                        // if no status record exists, fallback to null dates
                        const rec = stMap[step.id] || { started_at: null, completed_at: null };

const isDone = !!rec.completed_at;
    const isStarted = !!rec.started_at;
    const statusText = isDone
      ? "Completed"
      : isStarted
      ? "In progress"
      : "Pending";
                            const next = proc.steps[idx + 1];
                            return (
                              <tr key={step.id}>
                                <td style={styles.td}>{proc.name}</td>
                                <td style={styles.td}>{step.name}</td>
                                <td style={styles.td}>
                                  {next ? next.name : ""}
                                </td>
<td style={styles.td}>
          <div>
            <strong>{statusText}</strong>
            {isDone && (
              <div style={{ marginTop: 4, fontStyle: "italic", fontSize: 12 }}>
                Completed by: {step.responsibility || "Unknown"}
              </div>
            )}
          </div>
        </td>
                              </tr>
                            );
                          })}
                        </tbody>
                      </table>
                    </div>
                  </div>
                )}
              </div>
            </div>
          );
        })}
      </div>
    </div>
  );
}

const BLUE = "#007bff";

const styles: Record<string, React.CSSProperties> = {
  container: {
    padding: "20px",
    fontFamily: "Arial, sans-serif",
  },
  title: {
    textAlign: "center",
    fontSize: 20,
    marginBottom: 16,
  },
  loading: {
    textAlign: "center",
    padding: 20,
    fontSize: 16,
  },
  timelineContainer: {
    display: "flex",
    flexDirection: "column",
    gap: 16,
  },
  itemRow: {
    display: "flex",
  },
  timestamp: {
    fontSize: 14,
    color: "#555",
  },
  lineContainer: {
    position: "relative",
    width: 40,
    display: "flex",
    justifyContent: "center",
  },
  verticalLine: {
    position: "absolute",
    top: 0,
    bottom: 0,
    width: 3,
    background: BLUE,
  },
  dot: {
    width: 14,
    height: 14,
    borderRadius: "50%",
    background: "#fff",
    border: `3px solid ${BLUE}`,
    zIndex: 1,
    marginTop: 4,
  },
  card: {
    flex: 1,
    background: "#f0f0f0",
    borderRadius: 4,
    overflow: "hidden",
  },
  cardHeader: {
    display: "flex",
    alignItems: "center",
    background: "#e8e8e8",
  },
  headerField: {
    fontWeight: 500,
  },
  ttl: {
    fontWeight: 500,
  },
  expandedSection: {
    marginTop: 8,
    display: "flex",
    flexDirection: "column",
    gap: 8,
  },
  tableWrapper: {
    overflowX: "auto",
  },
  table: {
    width: "100%",
    borderCollapse: "collapse",
    minWidth: 400,
  },
  th: {
    border: "1px solid #ccc",
    padding: 8,
    background: "#ddd",
    textAlign: "left",
    fontSize: 12,
  },
  td: {
    border: "1px solid #ccc",
    padding: 8,
    fontSize: 12,
  },
};
