interface FieldInfo {
  key: string;
  title: string;
  type: string;
  options?: string[];
}





// near your FormBuilder type
interface FormField {
  id: string;
  label: string;
  type: 'text' | 'number' | 'date' | 'dropdown' | 'checkbox' | 'radio';
  options?: string[];
}

type FormBuilder = {
  id: string;
  fields: FormField[];
  actionMapping: string;
};


// Define the structure for a split target when stored as an object.
interface SplitTargetObject {
  processId: string;
  stepId: number;
  processName: string;
  stepName: string;
}

// Updated interface for each status row.
// NOTE: Added 'assign' as a possible actionMapping.
export interface StatusData {
  statusName: string;
  inputType: string;
  optionName: string;
  actionMapping: 'next' | 'stop' | 'split' | 'assign' | '';
  nextStep: boolean;
  split: boolean;
  nextStepId?: number;
  nextSequenceId?: number;
  splitTarget?: SplitTargetObject | string;
  selectedValue?: string;
  selectedOptions?: string[];
  alertEmail?: string;
  alertTemplate?: string;
  alertEnabled?: boolean;
}

// Define the checklist interface including an optional completedAt timestamp.
interface ChecklistItem {
  text: string;
  isChecked: boolean;
  completedAt?: string;
}

// The Step row from DB. (Includes step_type, statuses, and checklists)
interface StepFull {
  id: string;
  process_id: string;
  step_id: number;
  sequence_id: number;
  name: string;
  responsibility?: string; 
  tat_value?: number;
  tat_unit?: string;
  statuses?: StatusData[];
  checklists?: ChecklistItem[];
  form_url?: string; 
  crafty_action_mapping?: 'next' | 'stop' | 'split' | 'assign' | 'nextForm' | '';
  started_at?: string;
  completed_at?: string;   
  created_at?: string;    
  scheduled_at?: string;  // <-- new


    

  // Indicates if the step is "status" based or "checklist" based.
  step_type?: 'status' | 'checklist' | 'crafty';
  // For checklist-based steps, stores the action mapping to be triggered upon completion.
  checklistActionMapping?: 'next' | 'stop' | 'split' | 'assign' | '';
  is_escalated?: boolean;
  form_definition?: any[][]; // ideally: FormField[][]
  form_action_mapping?: string[];
 alerts?: AlertEntry[];
}

// Form submission row.
interface FormSubmission {
  id: string;
  fields?: string;
  data: Record<string, any>;
  responses?: string;
}

// Undo information structure.
interface UndoInfo {
  previousStatuses: StatusData[];
  previousStepId: string;
}

//  NEW: match AlertEntry from AddStep.tsx
interface AlertEntry {
  statusName: string;
type: ('email' | 'whatsapp')[];  
  email: string;
  template: string;
buttons?: { title: string; reply: string }[];
}


// Helper: extract the UUID from URLs like /shared-form/<uuid>
const extractFormId = (url: string): string | null => {
  try {
    const clean = url.split(/[?#]/)[0].replace(/\/+$/, '');
    const last = clean.split('/').pop()!;
    const uuidRegex = /^[0-9a-f]{8}-[0-9a-f]{4}-[0-9a-f]{4}-[0-9a-f]{4}-[0-9a-f]{12}$/i;
    return uuidRegex.test(last) ? last : null;
  } catch {
    return null;
  }
};


