useEffect(() => {
  if (!step || !step.form_definition) return;

  // 1) grab the raw
  let raw: any = step.form_definition;

  // 2) if Supabase gave us JSONtext, parse it:
  if (typeof raw === "string") {
    try {
      raw = JSON.parse(raw);
    } catch (err) {
      console.error("Could not parse form_definition JSON:", err, raw);
    }
  }

  // 3) if its an object whose keys are "0","1", turn it into an array:
  if (
    raw !== null &&
    typeof raw === "object" &&
    !Array.isArray(raw) &&
    Object.keys(raw).every(k => /^\d+$/.test(k))
  ) {
    raw = Object
      .keys(raw)
      .sort((a, b) => Number(a) - Number(b))
      .map(k => raw[k]);
  }

  // now ensure it really is an arrayofarrays
  if (!Array.isArray(raw)) {
    console.error("form_definition is not an array even after normalize:", raw);
    return;
  }

  // parse action mapping too (if its stored as text)
  let rawMap: any = step.form_action_mapping;
  let actionMap: string[] = [];
  if (typeof rawMap === "string") {
    try {
      actionMap = JSON.parse(rawMap);
    } catch {
      actionMap = rawMap.split(",");
    }
  } else if (Array.isArray(rawMap)) {
    actionMap = rawMap;
  }

  // 4) finally build your FormBuilder[]
  const defs: FormBuilder[] = raw.map((fieldsArr: any, i: number) => ({
    id: `form-${i}`,
    fields: Array.isArray(fieldsArr) ? fieldsArr : [],    // guard
    actionMapping: actionMap[i] || ""
  }));

  setFormDefinitions(defs);
  setCurrentFormIndex(0);
}, [step]);








