const gatherResponses = () => {
  const builder = formDefinitions[currentFormIndex];
  const out: Record<string, any> = {};

  builder.fields.forEach(field => {
    // use label as the key rather than the UUID
    const key = field.label;  
    out[key] = formResponses[field.id] ?? null;
  });

  return out;
};



