/**
 * Return a string like "2025-05-21 12:08:00" for the given date (or now) in IST.
 */
function getISTTimestamp(date?: Date): string {
  const base = date ?? new Date();
  // UTC ms + local offset  true UTC, then +5:30 IST
  const utcMs     = base.getTime() + base.getTimezoneOffset() * 60 * 1000;
  const istMs     = utcMs + 5.5 * 60 * 60 * 1000;
  const d         = new Date(istMs);
  const pad = (n: number) => String(n).padStart(2, '0');
  return `${d.getFullYear()}-${pad(d.getMonth()+1)}-${pad(d.getDate())}`
       + ` ${pad(d.getHours())}:${pad(d.getMinutes())}:${pad(d.getSeconds())}`;
}

  

