type CraftyAction = 'next' | 'stop' | 'split' | 'assign' | 'nextForm';


async function handleCraftySubmit(
  values: Record<string, any>,
  fields: string[]
) {
  if (!step) {
    console.error('No step loaded');
    return;
  }

  // 1 Save the submission (unchanged)
  const { data: inserted, error: insertErr } = await supabase
    .from('step_form_submissions')
    .insert([{
      process_id: step.process_id,
      fields,
      step_id:     step.step_id,
      
      sequence_id: step.sequence_id,
      responses: fields.map(k => values[k]),
      data: values,
    }])
    .select();

  if (insertErr) {
    console.error('Error saving submission:', insertErr);
    return alert(`Failed to save form: ${insertErr.message}`);
  }

    const now = getISTTimestamp();
  const { error: completeErr } = await supabase
    .from('steps')
    .update({ completed_at: now })
    .eq('id', step.id);

  if (completeErr) {
    console.error('Error completing step:', completeErr);
  } else {
    // also update local state so your UI rerenders with the timestamp
setStep(prev => {
  if (!prev) return prev;      // nothing to update
  return {
    ...prev,                   // all existing required fields
    completed_at: now          // just override this one
  };
});  }


  // 2 Determine which action to take
  let chosenAction: CraftyAction = craftyActionMapping || 'next';
  let splitTarget: string | undefined;

  // First check optionlevel mappings
//  in your optionlevel loop 
for (const key of fields) {
  const val = values[key];
  // raw can be either a string or an object
  const raw = mappingConfig.optionMapping[key]?.[val];
  // normalize to { action, target? }
  const entry: { action: string; target?: string } | undefined =
    raw == null
      ? undefined
      : typeof raw === 'object'
        ? raw
        : { action: raw };

  if (entry) {
    if (entry.action !== 'split') {
      chosenAction = entry.action as CraftyAction;
      break;
    } else {
      chosenAction = 'split';
      splitTarget = entry.target;
      break;
    }
  }
}


  // If still not split/next/stop etc, check fieldlevel
//  in your fieldlevel loop 
if (chosenAction === craftyActionMapping) {
  for (const key of fields) {
    const rawF = mappingConfig.fieldMapping[key];
    const fldEntry: { action: string; target?: string } | undefined =
      rawF == null
        ? undefined
        : typeof rawF === 'object'
          ? rawF
          : { action: rawF };

    if (fldEntry) {
      if (fldEntry.action !== 'split') {
        chosenAction = fldEntry.action as CraftyAction;
        break;
      } else {
        chosenAction = 'split';
        splitTarget = fldEntry.target;
        break;
      }
    }
  }
}


  console.log('Chosen action:', chosenAction, 'Split target:', splitTarget);

  // 3 Act on that action
  switch (chosenAction) {
    case 'next':
    case 'nextForm': {
      // try to go to the very next sequence
      const { data: nextStep, error: nextErr } = await supabase
        .from('steps')
        .select('id')
        .eq('process_id', step.process_id)
        .eq('sequence_id', step.sequence_id + 1)
        .single();

      if (nextErr || !nextStep?.id) {
        return alert('Next step not available');
      }
      return navigate(`/doer/step/${nextStep.id}/submission/nosub`);
    }

    case 'stop':
      return alert('Workflow complete');

    case 'split': {
      if (!splitTarget) {
        return alert('Split target missing');
      }
      // parse "Step: <stepName> in <processName>"
      const m = /^Step:\s*(.+?)\s+in\s+(.+)$/.exec(splitTarget);
      if (!m) {
        return alert('Malformed split target');
      }
      const [, stepName, processName] = m;

      // find process by name
      const { data: procRow } = await supabase
        .from('processes')
        .select('id')
        .eq('name', processName)
        .maybeSingle();
      if (!procRow?.id) {
        return alert(`Process "${processName}" not found`);
      }

      // find step by name within that process
      const { data: targetStep, error: tsErr } = await supabase
        .from('steps')
        .select('id')
        .eq('process_id', procRow.id)
        .eq('name', stepName)
        .single();

      if (tsErr || !targetStep?.id) {
        return alert(`Step "${stepName}" in "${processName}" not found`);
      }

      return navigate(`/doer/step/${targetStep.id}/submission/nosub`);
    }

    default:
      // fallback like "next"
      const { data: fb, error: fbErr } = await supabase
        .from('steps')
        .select('id')
        .eq('process_id', step.process_id)
        .eq('sequence_id', step.sequence_id + 1)
        .single();
      if (fb?.id && !fbErr) {
        return navigate(`/doer/step/${fb.id}/submission/nosub`);
      }
      return alert('Could not determine next action');
  }
}










function computeStatusDiff(
  prev: StatusData[],
  next: StatusData[]
): Array<{ index: number; field: string; previous: any; current: any }> {
  const diffs: Array<{ index: number; field: string; previous: any; current: any }> = [];
  const maxLen = Math.max(prev.length, next.length);

  for (let i = 0; i < maxLen; i++) {
    const p = prev[i] || ({} as StatusData);
    const n = next[i] || ({} as StatusData);

    // List out the keys you want to track:
    for (const key of ['selectedValue', 'statusName', 'optionName', 'actionMapping'] as const) {
      if ((p as any)[key] !== (n as any)[key]) {
        diffs.push({
          index:   i,
          field:   key,
          previous: (p as any)[key] ?? null,
          current:  (n as any)[key] ?? null
        });
      }
    }
  }

  return diffs;
}

