const handleUndo = async () => {
  const undoInfo = (location.state as { undoInfo?: UndoInfo })?.undoInfo;
  if (!undoInfo) return;

  const { previousStepId, previousStatuses } = undoInfo;

  // 1 In the database, clear the completed_at timestamp and restore old statuses
  const { error } = await supabase
    .from('steps')
    .update({
      completed_at: null,
      statuses: previousStatuses
    })
    .eq('id', previousStepId);

  if (error) {
    console.error('Error reverting step completion and statuses:', error);
    return alert('Failed to undo step completion.');
  }

  // 2 In React state, clear completed_at and reset statuses
  setStep(prev =>
    prev && prev.id === previousStepId
      ? {
          ...prev,
          completed_at: undefined,
          statuses: previousStatuses
        }
      : prev
  );

  // 3 Let the user re-click Complete Step
  alert('Undo successful. Please click Complete Step again when ready.');
  navigate(`/doer/step/${previousStepId}/submission/nosub`);
};

