async function handleStartStep() {
  if (!step || !submission) return;

  try {
    // 1 Check if already started
    const { data: existingStatus, error: fetchErr } = await supabase
      .from('submission_step_statuses')
      .select('started_at')
      .eq('submission_id', submission.id)
      .eq('step_id', step.id)
      .single();

    if (fetchErr && fetchErr.code !== 'PGRST116') { // PGRST116 = No rows found (acceptable)
      console.error('Failed to check started_at:', fetchErr.message);
      return;
    }

    if (existingStatus?.started_at) {
      console.log(' Step already started at:', existingStatus.started_at);
      return; // already started, do nothing
    }

    // 2 Insert started_at timestamp
    const timestamp = getISTTimestamp();
    const { error: startErr } = await supabase
      .from('submission_step_statuses')
      .upsert({
        submission_id: submission.id,
        step_id: step.id,
        started_at: timestamp,
        statuses: step.statuses || [],
      }, { onConflict: 'submission_id,step_id' });

    if (startErr) {
      console.error(' Failed to record start:', startErr.message);
      return;
    }

    console.log(' Step started at', timestamp);

    // 3 Update local state (optional, for UI reactivity)
    setStep(prev => prev ? { ...prev, started_at: timestamp } : prev);

  } catch (err) {
    console.error('Unexpected error in handleStartStep:', err);
  }
}




// Marks the current crafty step as completed, then navigates to the very next sequence_id in the same process.
const handleCompleteCrafty = async () => {
  if (!step) return alert('No current step available.');

  // 1 Mark this step done
  const { error: completeError } = await supabase
    .from('steps')
    .update({ is_completed: true, completed_at: new Date().toISOString() })
    .eq('id', step.id);

  if (completeError) {
    console.error('Error marking crafty step completed:', completeError);
    return alert('Failed to complete the step.');
  }

  // 2 Fetch the nextinline step
  const { data: nextStep, error: nextErr } = await supabase
    .from('steps')
    .select('id')
    .eq('process_id', step.process_id)
    .eq('sequence_id', step.sequence_id + 1)
    .single();

  if (nextErr || !nextStep) {
    return alert('No next step available.');
  }

  // 3 Redirect into the Doer view of that step
  navigate(`/doer/step/${nextStep.id}/submission/nosub`);
};



// 2 Updated handleCompleteStep: runs your automation only on button click,
