// inside DoerStepDetail.tsx
const handleCompleteStep = async () => {
  // 0 Guard
  if (!step || !submission) {
    return alert('Step or submission not loaded.');
  }






  
  // 1 Validation
  if (step.step_type === 'checklist') {
    const allDone = step.checklists?.every(item => item.isChecked);
    if (!allDone) {
      return alert('Please finish all checklist items before completing.');
    }
  }
  if (step.step_type === 'status') {
    const picked = submissionStatuses?.find(st => st.selectedValue?.trim());
    if (!picked) {
      return alert('Please select a status before completing.');
    }
  }

  // 2 Build upsert payload
  const now = getISTTimestamp();

  // 1 Persist the completion timestamp (and any final statuses):
  const { error: upsertErr } = await supabase
    .from('submission_step_statuses')
    .upsert({
      submission_id: submission.id,
      step_id:       step.id,
      completed_at:  now,
      statuses:      submissionStatuses || step.statuses || [],
    }, { onConflict: 'submission_id,step_id' });

  if (upsertErr) {
    console.error('Error recording completion:', upsertErr.message);
    return alert('Failed to complete this step:\n' + upsertErr.message);
  }


    //  NEW: Log step completion
  await supabase
    .from('logs')
    .insert({
      event_type: 'step_complete',
      details: JSON.stringify({
        stepId:       step.id,
        submissionId: submission.id,
        completed_at: now,
        statuses:     submissionStatuses
      }),
      created_at: now
    });

  // 4 (Optional) Refresh your local completions or disable the button here

  // 5 Fetch next step in this process
  const { data: nextStep, error: nextErr } = await supabase
    .from('steps')
    .select('id')
    .eq('process_id', step.process_id)
    .eq('sequence_id', step.sequence_id + 1)
    .single();

  if (nextErr || !nextStep?.id) {
    alert('All steps done for this submission!');
    return navigate('/doer/steplist');
  }

  // 6 Navigate this submission to its next step
  navigate(`/doer/step/${nextStep.id}/submission/${submission.id}`);
};








