// src/components/ProcessList.tsx
import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import { supabase } from "../supabaseClient";

interface StepData {
  id: string;
  tat_value?: number | null;
  tat_unit?: string; // Supports units like 'mins', 'hrs', 'days', 'weeks'
}

interface FormSubmissionData {
  id: string;
}

interface ProcessData {
  id: string;
  name: string;
  steps?: StepData[];
  form_submissions?: FormSubmissionData[];
}

// Helper function to convert a TAT value to hours based on its unit.
const convertToHours = (value: number, unit: string = "hrs"): number => {
  switch (unit) {
    case "mins":
      return value / 60;
    case "days":
      return value * 24;
    case "weeks":
      return value * 168; // 7 * 24
    case "hrs":
    default:
      return value;
  }
};

const ProcessList: React.FC = () => {
  const navigate = useNavigate();
  const [processes, setProcesses] = useState<ProcessData[]>([]);
  const [openMenuId, setOpenMenuId] = useState<string | null>(null);
  const [searchTerm, setSearchTerm] = useState("");

  const [randomCount, setRandomCount] = useState<number>(0);


  useEffect(() => {
    fetchProcesses();
    fetchRandomCount();     // + also load random-task count
  }, []);


   // + Fetch count of steps with process_id = null
 const fetchRandomCount = async () => {
   const { data: { session } } = await supabase.auth.getSession();
   if (!session?.user) return;
   const userId = session.user.id;
   const { count, error } = await supabase
     .from("steps")
     .select("id", { head: true, count: "exact" })
     .is("process_id", null)
     .eq("user_id", userId);
   if (!error && count !== null) {
     setRandomCount(count);
   }
 };

  const fetchProcesses = async () => {
    // Get the current session to obtain the user_id.
    const { data: { session } } = await supabase.auth.getSession();
    if (!session || !session.user) {
      console.error("No user session found.");
      return;
    }
    const userId = session.user.id;
    const { data, error } = await supabase
      .from("processes")
      .select(`
        id,
        name,
        steps (id, tat_value, tat_unit),
        form_submissions (id)
      `)
      .eq("user_id", userId) // Filter by the logged in user's ID.
      .order("created_at", { ascending: false });

    if (error) {
      console.error("Error fetching processes:", error);
      return;
    }
    if (data) {
      setProcesses(data);
    }
  };

  const handleAddProcess = () => {
    navigate("/add-process");
  };

  const handleClickProcess = (procId: string) => {
    navigate(`/process/${procId}/steps`);
  };

  const toggleMenu = (
    e: React.MouseEvent<HTMLSpanElement, MouseEvent>,
    procId: string
  ) => {
    e.stopPropagation();
    setOpenMenuId((prev) => (prev === procId ? null : procId));
  };

  const handleEditProcess = async (procId: string, oldName: string) => {
    const newName = prompt("Enter new process name:", oldName);
    if (!newName || !newName.trim()) return;

    const { error } = await supabase
      .from("processes")
      .update({ name: newName })
      .eq("id", procId);

    if (error) {
      console.error("Error updating process:", error);
      alert("Failed to update process");
    } else {
      fetchProcesses();
      setOpenMenuId(null);
    }
  };

  const handleDeleteProcess = async (procId: string) => {
    if (!window.confirm("Are you sure you want to delete this process?")) return;

    const { error } = await supabase
      .from("processes")
      .delete()
      .eq("id", procId);

    if (error) {
      console.error("Error deleting process:", error);
      alert("Failed to delete process");
    } else {
      fetchProcesses();
      setOpenMenuId(null);
    }
  };

  // ------------------ Inline Styles ------------------
  const containerStyle: React.CSSProperties = {
    maxWidth: "600px",
    margin: "auto",
    padding: "1rem",
    fontFamily: "Arial, sans-serif",
  };

  const titleStyle: React.CSSProperties = {
    fontSize: "1.5rem",
    marginBottom: "1rem",
  };

  const searchBarStyle: React.CSSProperties = {
    display: "flex",
    gap: "0.5rem",
    marginBottom: "1rem",
  };

  const searchInputStyle: React.CSSProperties = {
    flex: 1,
    padding: "0.5rem",
  };

  const addButtonStyle: React.CSSProperties = {
    padding: "0.5rem 1rem",
    backgroundColor: "#333",
    color: "#fff",
    border: "none",
    borderRadius: "4px",
    cursor: "pointer",
  };

  const processCardStyle: React.CSSProperties = {
    position: "relative",
    backgroundColor: "#eee",
    borderRadius: "8px",
    padding: "1rem",
    marginBottom: "0.5rem",
    cursor: "pointer",
  };

  const detailsRowStyle: React.CSSProperties = {
    display: "flex",
    justifyContent: "space-between",
    alignItems: "center",
  };

  const processTimeStyle: React.CSSProperties = {
    margin: "0",
    color: "#666",
    fontWeight: "bold",
  };

  const menuIconStyle: React.CSSProperties = {
    position: "absolute",
    top: "8px",
    right: "8px",
    cursor: "pointer",
    fontSize: "1.2rem",
  };

  const menuDropdownStyle: React.CSSProperties = {
    position: "absolute",
    top: "32px",
    right: "8px",
    background: "#fff",
    border: "1px solid #ccc",
    borderRadius: "4px",
    padding: "0.25rem",
    zIndex: 2,
  };

  const menuItemStyle: React.CSSProperties = {
    padding: "0.5rem",
    cursor: "pointer",
  };

  // Filter processes based on the search term.
  const filteredProcesses = processes.filter((proc) =>
    proc.name.toLowerCase().includes(searchTerm.toLowerCase())
  );

  return (
    <div style={containerStyle}>
      <h1 style={titleStyle}>Define Processes</h1>

      {/* Search & Add Section */}
      <div style={searchBarStyle}>
        <input
          type="text"
          placeholder="Search..."
          style={searchInputStyle}
          value={searchTerm}
          onChange={(e) => setSearchTerm(e.target.value)}
        />
        <button style={addButtonStyle} onClick={handleAddProcess}>
          + Add a Process
        </button> 
                {/* <button
          style={addButtonStyle}
          onClick={() => navigate('/add-task')}
        >
          + Add Random Task
        </button>         */}
<button
          style={addButtonStyle}
          onClick={() => navigate('/random-task-list')}
        >
          Random Task List ({randomCount})   {/* + show live count */}
        </button>
      </div>

      {/* Process List */}
      {filteredProcesses.map((proc) => {
        const stepCount = proc.steps?.length ?? 0;
        const submissionCount = proc.form_submissions?.length ?? 0;
        // Convert each step's tat_value to hours using its tat_unit and sum them
        // const totalTat =
        //   proc.steps?.reduce(
        //     (acc, step) =>
        //       acc + convertToHours(step.tat_value || 0, step.tat_unit || "hrs"),
        //     0
        //   ) ?? 0;

          // 1 Compute total TAT in minutes
        const totalTatMins =
          proc.steps?.reduce((sum, step) => {
            const v = step.tat_value || 0;
            switch (step.tat_unit) {
              case "mins": return sum + v;
              case "hrs":  return sum + v * 60;
              case "days": return sum + v * 1440;
              case "weeks":return sum + v * 10080;
              default:     return sum;
            }
          }, 0) ?? 0;

        // 2 Format for display
        let tatDisplay: string;
        if (totalTatMins >= 1440) {
          tatDisplay = (totalTatMins / 1440).toFixed(1) + " days";
        } else if (totalTatMins >= 60) {
          tatDisplay = (totalTatMins / 60).toFixed(1) + " hrs";
        } else {
          tatDisplay = totalTatMins + " mins";
        }

        return (
          <div
            key={proc.id}
            style={processCardStyle}
            onClick={() => handleClickProcess(proc.id)}
          >
            <h3 style={{ margin: "0 0 0.5rem 0" }}>{proc.name}</h3>
            <div style={detailsRowStyle}>
              <p style={{ margin: 0 }}>
                {stepCount} Steps  {submissionCount} data points
              </p>
              <p style={processTimeStyle}>{tatDisplay}</p>
            </div>
            <span
              style={menuIconStyle}
              onClick={(e) => toggleMenu(e, proc.id)}
            >
              
            </span>
            {openMenuId === proc.id && (
              <div style={menuDropdownStyle} onClick={(e) => e.stopPropagation()}>
                <div
                  style={menuItemStyle}
                  onClick={() => handleEditProcess(proc.id, proc.name)}
                >
                  Edit
                </div>
                <div
                  style={menuItemStyle}
                  onClick={() => handleDeleteProcess(proc.id)}
                >
                  Delete
                </div>
              </div>
            )}
          </div>
        );
      })}
    </div>
  );
};

export default ProcessList;
