// src/components/RandomTaskList.tsx
import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import { supabase } from "../supabaseClient";

interface StepData {
  id: string;
  name?: string;
  tat_value?: number | null;
  tat_unit?: string; // 'mins', 'hrs', 'days', 'weeks'
  responsibility?: string;
}

const toMinutes = (value: number = 0, unit: string = "hrs"): number => {
  switch (unit) {
    case "mins":  return value;
    case "hrs":   return value * 60;
    case "days":  return value * 1440;
    case "weeks": return value * 10080;
    default:      return value;
  }
};

const RandomTaskList: React.FC = () => {
  const navigate = useNavigate();
  const [steps, setSteps] = useState<StepData[]>([]);
  const [loading, setLoading] = useState<boolean>(true);
  const [openMenuId, setOpenMenuId] = useState<string | null>(null);

  useEffect(() => {
    fetchUnassignedSteps();
  }, []);

  const fetchUnassignedSteps = async () => {
    setLoading(true);
    const {
      data: { session },
    } = await supabase.auth.getSession();
    if (!session?.user) {
      console.error("No active session");
      setLoading(false);
      return;
    }
    const userId = session.user.id;
    const { data, error } = await supabase
      .from("steps")
      .select("id, name, tat_value, tat_unit, responsibility")
      .is("process_id", null)
      .eq("user_id", userId)
      .order("created_at", { ascending: false });

    if (error) console.error("Fetch error:", error);
    else if (data) setSteps(data as StepData[]);
    setLoading(false);
  };

  const handleDelete = async (stepId: string) => {
    if (!window.confirm("Delete this step?")) return;
    const { error } = await supabase.from("steps").delete().eq("id", stepId);
    if (error) console.error("Delete error:", error);
    else fetchUnassignedSteps();
  };

  const handleEdit = (stepId: string) => {
    // Assuming your AddStep route is e.g. `/add-step/:flowchartId?mode=edit&stepId=...`
    // Here process_id is null, so we can navigate without flowchartId:
    navigate(`/add-task/?mode=edit&stepId=${stepId}`);
  };

  if (loading) return <div style={{ padding: "1rem" }}>Loading tasks...</div>;
  if (!steps.length) return <div style={{ padding: "1rem" }}>No unassigned steps.</div>;

  return (
    <div style={{ maxWidth: 700, margin: "auto", padding: "1rem", fontFamily: "Arial, sans-serif" }}>
      <div style={{ display: "flex", alignItems: "center", marginBottom: "1rem" }}>
        <button onClick={() => navigate(-1)} style={{ marginRight: "0.5rem" }}> Back</button>
        <h1 style={{ flex: 1, fontSize: "1.5rem", margin: 0 }}>Random Task List</h1>
        <button onClick={() => navigate("/add-task")} style={{ marginLeft: "0.5rem" }}>
          + Add a Step
        </button>

      </div>

      {steps.map((step) => {
        const totalMins = toMinutes(step.tat_value ?? 0, step.tat_unit ?? "hrs");
        let displayTAT = totalMins >= 1440
          ? (totalMins / 1440).toFixed(1) + " days"
          : totalMins >= 60
            ? (totalMins / 60).toFixed(1) + " hrs"
            : totalMins + " mins";

        return (
          <div
            key={step.id}
            style={{
              position: "relative",
              backgroundColor: "#f5f5f5",
              borderRadius: 8,
              padding: "1rem",
              marginBottom: "0.75rem"
            }}
          >
            {/* Threedot menu */}
            <span
              onClick={(e) => {
                e.stopPropagation();
                setOpenMenuId(openMenuId === step.id ? null : step.id);
              }}
              style={{
                position: "absolute",
                top: 8,
                right: 8,
                cursor: "pointer",
                fontSize: "1.2rem"
              }}
            >
              
            </span>
            {openMenuId === step.id && (
              <div
                onClick={(e) => e.stopPropagation()}
                style={{
                  position: "absolute",
                  top: 28,
                  right: 8,
                  background: "#fff",
                  border: "1px solid #ccc",
                  borderRadius: 4,
                  zIndex: 10
                }}
              >
                <div
                  onClick={() => handleEdit(step.id)}
                  style={{ padding: "0.5rem 1rem", cursor: "pointer" }}
                >
                  Edit
                </div>
                <div
                  onClick={() => handleDelete(step.id)}
                  style={{ padding: "0.5rem 1rem", cursor: "pointer" }}
                >
                  Delete
                </div>
              </div>
            )}

            <div
              style={{ display: "flex", justifyContent: "space-between", alignItems: "center", cursor: "pointer" }}
              onClick={() => navigate(`/task/${step.id}`)}
            >
              <div>
                <h3 style={{ margin: "0 0 0.5rem" }}>{step.name || "Unnamed Step"}</h3>
                <p style={{ margin: 0, color: "#555" }}>
                  {step.responsibility || "No role specified"}
                </p>
              </div>
              <div style={{ fontWeight: "bold", color: "#333" }}>
                {displayTAT}
              </div>
            </div>
          </div>
        );
      })}
    </div>
  );
};

export default RandomTaskList;
