// src/pages/ReportPage.tsx
import React, {
  useEffect,
  useState,
  useRef,
  createRef,
  type RefObject,
} from "react";
import { supabase } from "../supabaseClient";
import { useNavigate } from 'react-router-dom';
import {
  FiClock,
  FiChevronDown,
  FiChevronUp,
  FiArrowRight,
} from "react-icons/fi";
import type { IconBaseProps } from "react-icons";

const ClockIcon = FiClock as React.ComponentType<IconBaseProps>;
const ChevronDown = FiChevronDown as React.ComponentType<IconBaseProps>;
const ChevronUp = FiChevronUp as React.ComponentType<IconBaseProps>;
const ArrowRight = FiArrowRight as React.ComponentType<IconBaseProps>;

type ViewMode = "process" | "steps" | "doer";

interface StatusData {
  statusName: string;
}

interface Step {
  id: string;
  name: string;
  created_at: string;
  started_at: string | null;
  completed_at: string | null;
  tat_value: number | null;
  tat_unit: string | null;
  statuses?: StatusData[];
}

interface Submission {
  id: string;
}

interface ProcessWithDetails {
  id: string;
  name: string;
  created_at: string;
  steps: Step[];
  form_submissions: Submission[];
}

//  Helpers 

const toHours = (v: number, u: string = "hrs") =>
  u === "mins" ? v / 60 : u === "days" ? v * 24 : u === "weeks" ? v * 168 : v;

function formatDuration(ms: number) {
  const t = Math.abs(ms),
    h = Math.floor(t / 3_600_000),
    m = Math.floor((t % 3_600_000) / 60_000);
  return `${h}h ${m}m`;
}

/**
 * Format a millisecond delay into minutes, hours, or days.
 */
function formatDelayAmount(ms: number): string {
  const abs = Math.abs(ms);
  const mins = abs / 60_000;
  if (mins < 60) {
    return `${Math.round(mins)} mins`;
  }
  const hrs = abs / 3_600_000;
  if (hrs < 24) {
    return `${hrs.toFixed(1)} hrs`;
  }
  const days = abs / 86_400_000;
  return `${days.toFixed(1)} days`;
}



function formatIST(date: Date): string {
  const d = date;
  const pad = (n: number) => String(n).padStart(2, "0");

  const Y = d.getFullYear();
  const M = pad(d.getMonth() + 1);
  const D = pad(d.getDate());
  const h = pad(d.getHours());
  const m = pad(d.getMinutes());
  const s = pad(d.getSeconds());

  return `${Y}-${M}-${D} ${h}:${m}:${s}`;
}

function parseLocalDate(ts: string): Date {
  return new Date(ts.replace(" ", "T"));
}

function computeOverdueMs(step: Step): number {
  if (!step.started_at || step.tat_value == null) return 0;
  const start = parseLocalDate(step.started_at).getTime();
  const end = step.completed_at
    ? parseLocalDate(step.completed_at).getTime()
    : Date.now();
  const expectedMs = toHours(step.tat_value, step.tat_unit || "hrs") * 3_600_000;
  return end - start - expectedMs;
}

function getStepBackground(step: Step): string {
  if (step.completed_at) return "#f6ffed";
  if (step.started_at) {
    const overdueMs = computeOverdueMs(step);
    return overdueMs > 0 ? "#fff1f0" : "#fffbe6";
  }
  return "#ffffff";
}

function getStepStatusText(step: Step): string {
  if (step.completed_at) return "Completed";
  if (step.started_at) return "In Progress";
  return "Pending";
}

function statusColors(status: string): [string, string] {
  switch (status) {
    case "Completed":
      return ["#f6ffed", "#389e0d"];
    case "In Progress":
      return ["#fffbe6", "#ad8b00"];
    default:
      return ["#fff", "#595959"];
  }
}

function formatOverdueText(step: Step): string {
  const overdueMs = computeOverdueMs(step);
  if (overdueMs > 0) {
    return `  Delay ${formatDelayAmount(overdueMs)}`;
  }
  return "";
}

// function computeDuration(step: Step): string {
//   if (!step.started_at) return "N/A";
//   const start = parseLocalDate(step.started_at).getTime();
//   const end = step.completed_at
//     ? parseLocalDate(step.completed_at).getTime()
//     : Date.now();
//   return formatDuration(end - start);
// }

/**
 * Compute running duration (from started_at  completed_at or now)
 * and format it as mins/hrs/days.
 * If not started, returns "N/A".
 */
function computeDuration(step: Step): string {
  if (!step.started_at) return "N/A";
  const start = parseLocalDate(step.started_at).getTime();
  const end = step.completed_at
    ? parseLocalDate(step.completed_at).getTime()
    : Date.now();
  return formatSpan(end - start);
}





/**
 * Format a millisecond span into minutes, hours, or days.
 */
function formatSpan(ms: number): string {
  const abs = Math.abs(ms);
  const mins = abs / 60_000;
  if (mins < 60) {
    return `${Math.round(mins)} mins`;
  }
  const hrs = abs / 3_600_000;
  if (hrs < 24) {
    return `${hrs.toFixed(1)} hrs`;
  }
  const days = abs / 86_400_000;
  return `${days.toFixed(1)} days`;
}


/**
 * Compute the gap between two steps: time from prev.completed_at  next.started_at
 */
function computeGap(prev: Step, next: Step): string | null {
  if (!prev.completed_at || !next.started_at) return null;
  const endPrev = parseLocalDate(prev.completed_at).getTime();
  const startNext = parseLocalDate(next.started_at).getTime();
  if (startNext <= endPrev) return null;
  return formatDuration(startNext - endPrev);
}

//  Component 

const ReportPage: React.FC = () => {
  const navigate = useNavigate();
  const [processes, setProcesses] = useState<ProcessWithDetails[]>([]);
    // +: store per-submission step status records
  const [stepStatuses, setStepStatuses] = useState<
    Record<string, Record<string, { started_at: string | null; completed_at: string | null }>>
  >({});
  const [loading, setLoading] = useState(true);
  const [expanded, setExpanded] = useState<Set<string>>(new Set());
  const [sortAsc, setSortAsc] = useState(false);
  const [viewMode, setViewMode] = useState<ViewMode>("process");
  const [focusProcess, setFocusProcess] = useState<string | null>(null);

  const cardRefs = useRef<Record<string, RefObject<HTMLDivElement | null>>>({});

  useEffect(() => {
    loadProcesses();
  }, [sortAsc]);

  useEffect(() => {
    if (focusProcess && cardRefs.current[focusProcess]) {
      cardRefs.current[focusProcess].current?.scrollIntoView({
        behavior: "smooth",
        block: "center",
      });
    }
  }, [viewMode, focusProcess]);

  async function loadProcesses() {
    setLoading(true);
    const {
      data: { session },
    } = await supabase.auth.getSession();

    if (!session?.user) {
      setProcesses([]);
      setLoading(false);
      return;
    }
    const { data, error } = await supabase
      .from("processes")
      .select(`
        id,name,created_at,
        steps (
          id,name,created_at,started_at,completed_at,
          tat_value,tat_unit,statuses
        ),
        form_submissions(id)
      `)
      .eq("user_id", session.user.id)
      .order("created_at", { ascending: sortAsc })
      .order("created_at", { ascending: true, foreignTable: "steps" });

    const procs = error ? [] : (data as ProcessWithDetails[]);

    const refs: Record<string, RefObject<HTMLDivElement | null>> = {};
    procs.forEach((p) => {
      refs[p.id] = createRef<HTMLDivElement | null>();
    });
    cardRefs.current = refs;

    setProcesses(procs);
        // +: now fetch submission_step_statuses for all submissions
    const submissionIds = procs.flatMap((p) =>
      p.form_submissions.map((s) => s.id)
    );
    if (submissionIds.length) {
      const { data: statusesData } = await supabase
        .from("submission_step_statuses")
        .select("submission_id,step_id,started_at,completed_at")
        .in("submission_id", submissionIds);
      const map: typeof stepStatuses = {};
      statusesData?.forEach((r) => {
        map[r.submission_id] ||= {};
        map[r.submission_id][r.step_id] = {
          started_at: r.started_at,
          completed_at: r.completed_at,
        };
      });
      setStepStatuses(map);
    }
    setLoading(false);
  }




  function toggleExpand(id: string) {
    setExpanded((prev) => {
      const nxt = new Set(prev);
      nxt.has(id) ? nxt.delete(id) : nxt.add(id);
      return nxt;
    });
  }

  function onArrowClick(processId: string) {
    setViewMode("process");
    setExpanded((prev) => new Set(prev).add(processId));
    setFocusProcess(processId);
  }

  // +: derive status from fetched stepStatuses rather than static timestamps
  function getStatus(proc: ProcessWithDetails, step: Step) {
    const subId = proc.form_submissions[0]?.id;
    // +: ensure rec is either StatusRecord or undefined
    const rec = subId
      ? stepStatuses[subId]?.[step.id]
      : undefined;
    if (rec?.completed_at) return { text: "Completed", start: rec.started_at, end: rec.completed_at };
    if (rec?.started_at)   return { text: "In Progress", start: rec.started_at, end: null };
    return { text: "Pending", start: null, end: null };
  }

  const renderStatusPill = (proc: ProcessWithDetails, step: Step) => {
    const { text: statusText } = getStatus(proc, step);
    const [bg, fg] = statusColors(statusText);
    return (
      <span style={{ ...styles.statusPill, background: bg, color: fg }}>
        {statusText}
      </span>
    );
  };

  const renderProcessView = () => (
    <div style={styles.timeline}>
      {processes.map((proc) => {
        const total = proc.steps.length;
        const done = proc.steps.filter((s) => !!s.completed_at).length;
        const pct = total > 0 ? Math.round((done / total) * 100) : 0;
        const stepCount = total;
        const dataCount = proc.form_submissions.length;
        const totalTat = proc.steps
          .reduce(
            (s, st) => s + toHours(st.tat_value || 0, st.tat_unit || "hrs"),
            0
          )
          .toFixed(1);

        return (
          <div
            key={proc.id}
            ref={cardRefs.current[proc.id]}
            style={styles.entry}
          >
            <div style={styles.dateCell}>
              <div style={styles.dateText}>
                {(() => {
                  const starts = proc.steps
                    .map((s) => s.started_at)
                    .filter((ts): ts is string => !!ts);
                  if (!starts.length) return "";
                  const first = starts.sort()[0];
                  return formatIST(parseLocalDate(first));
                })()}
              </div>
              <ClockIcon style={styles.clockIcon} />
            </div>
            <div style={styles.lineContainer}>
              <div style={styles.line} />
            </div>
            <div style={styles.card}>
              <div style={styles.cardHeader}>
                <strong style={{ fontSize: 18 }}>{proc.name}</strong>
                <button
                  onClick={() => toggleExpand(proc.id)}
                  style={styles.collapseBtn}
                >
                  {expanded.has(proc.id) ? <ChevronUp /> : <ChevronDown />}
                </button>
              </div>

              <div style={styles.tagsRow}>
                <span
                  style={{ ...styles.tag, background: "#E6FFFB", color: "#006D75" }}
                >
                  {stepCount} Steps
                </span>
                <span
                  style={{ ...styles.tag, background: "#F6FFED", color: "#52C41A" }}
                >
                  {dataCount} Data pts
                </span>
                <span
                  style={{ ...styles.tag, background: "#FFF2E8", color: "#FA541C" }}
                >
                  {totalTat} hrs
                </span>
              </div>

              <div style={styles.progressContainer}>
                <div style={{ ...styles.progressInner, width: `${pct}%` }} />
              </div>
              <span style={styles.progressLabel}>
                {done} / {total} steps ({pct}%)
              </span>

              {expanded.has(proc.id) && (
                <div style={styles.stepsContainer}>
                  {proc.steps.map((step, idx) => {
                    // +: compute BG/text from live statuses
                    const { text: statusText, start, end } = getStatus(proc, step);
                    const gapText = idx > 0
                      ? computeGap(proc.steps[idx - 1], step)
                      : null;
                    const [bgColor] = statusColors(statusText);

                    return (
                      <React.Fragment key={step.id}>
                        {gapText && (
                          <div style={styles.gapRow}>
                            <ClockIcon style={styles.gapIcon} />
                            <span style={styles.gapText}>
                              {`Gap: ${gapText}`}
                            </span>
                          </div>
                        )}
                        <div style={{ ...styles.stepCard, background: bgColor }}>
                          <div style={styles.stepHeader}>
                            <strong>{step.name}</strong>
                            {renderStatusPill(proc, step)}
                         </div>
                          <div style={styles.stepMeta}>
                            {start
                              ? formatIST(parseLocalDate(start))
                              : ""}{" "}
                             Duration: {start
                                ? formatSpan(
                                    (end
                                      ? parseLocalDate(end).getTime()
                                      : Date.now()
                                    ) - parseLocalDate(start).getTime()
                                  )
                                : "N/A"
                              }{!end && start ? " (ongoing)" : ""}
                          </div>
                        </div>
                      </React.Fragment>
                    );
                  })}
                </div>
              )}
            </div>
          </div>
        );
      })}
    </div>
  );

  const renderStepView = () => {
    const all = processes.flatMap((p) =>
      p.steps.map((st) => ({ ...st, processName: p.name, processId: p.id }))
    );
    return (
      <div style={styles.stepsContainer}>
        {all.map((st) => {
          // +: derive BG & pill from statuses
          const { text: statusText, start, end } = getStatus(
            { ...processes.find(p => p.id === st.processId)! }, st
          );
          const [bgColor] = statusColors(statusText);

          return (
            <div key={st.id} style={{ ...styles.stepCard, background: bgColor }}>
              <div style={styles.stepHeader}>
                <strong>
                  {st.processName}: {st.name}
                </strong>
                {renderStatusPill(
                   processes.find(p => p.id === st.processId)!,
                   st
                 )}
                <button
                  onClick={() => onArrowClick(st.processId)}
                  style={styles.arrowBtn}
                >
                  <ArrowRight />
                </button>
              </div>
              <div style={styles.statusSequence}>
                {Array.isArray(st.statuses) && st.statuses.length > 0 ? (
                  st.statuses.map((s, i) => (
                    <span key={i} style={styles.sequenceItem}>
                      {s.statusName}
                      {i < st.statuses!.length - 1 ? "  " : ""}
                    </span>
                  ))
                ) : (
                  <span style={styles.sequenceItem}>No statuses</span>
                )}
              </div>
              <div style={styles.stepMeta}>
                Started:{" "}
                {st.started_at ? formatIST(parseLocalDate(st.started_at)) : ""}
                <br />
                Duration: {computeDuration(st)} {formatOverdueText(st)}
              </div>
            </div>
          );
        })}
      </div>
    );
  };

  return (
    <div style={styles.container}>
      <h2 style={styles.title}>Process Timelineee</h2>

      <div style={{ marginBottom: 16 }}>
        <button
          style={viewMode === "process" ? styles.filterActive : styles.filterBtn}
          onClick={() => setViewMode("process")}
        >
          By Process
        </button>
        <button
          style={viewMode === "steps" ? styles.filterActive : styles.filterBtn}
          onClick={() => setViewMode("steps")}
        >
          By Step
        </button>
                <button
          style={styles.filterBtn}
          onClick={() => navigate('/report/processstepdatapoints')}
        >
          New Report Page
        </button>
        <button
          style={styles.filterBtn}
          onClick={() => navigate('/report/datapoints')}
        >
         By Data Points
        </button>
                <button
          style={viewMode === 'doer' ? styles.filterActive : styles.filterBtn}
          onClick={() => navigate('/report/doer')}
        >
         By Doer
        </button>
        
      </div>

      <div style={styles.sortToggle}>
        <button
          style={{
            ...styles.sortBtn,
            ...(sortAsc ? {} : styles.sortBtnActive),
          }}
          onClick={() => setSortAsc(true)}
        >
          Oldest first
        </button>
        <button
          style={{
            ...styles.sortBtn,
            ...(sortAsc ? styles.sortBtnActive : {}),
          }}
          onClick={() => setSortAsc(false)}
        >
          Newest first
        </button>
      </div>

      {loading ? (
        <p>Loading</p>
      ) : viewMode === "process" ? (
        renderProcessView()
      ) : (
        renderStepView()
      )}
    </div>
  );
};

export default ReportPage;

//  Inline Styles 
