// src/pages/ReportPage.tsx
import React, {
  useEffect,
  useState,
  useRef,
  createRef,
  type RefObject,
} from "react";
import { supabase } from "../supabaseClient";
import { useNavigate } from "react-router-dom";
import {
  FiClock as RawFiClock,
  FiChevronDown,
  FiChevronRight,
} from "react-icons/fi";
import type { IconBaseProps } from "react-icons";

const ClockIcon = RawFiClock as React.ComponentType<IconBaseProps>;
const ChevronDown = FiChevronDown as React.ComponentType<IconBaseProps>;
const ChevronRight = FiChevronRight as React.ComponentType<IconBaseProps>;

interface Step {
  id: string;
  name: string;
  created_at: string;
  tat_value?: number;
  tat_unit?: string;
  responsibility?: string;
}
interface Submission {
  id: string;
  data?: Record<string, any>;
}
interface ProcessWithDetails {
  id: string;
  name: string;
  created_at: string;
  steps: Step[];
  form_submissions: Submission[];
}
interface StatusRecord {
  submission_id: string;
  step_id: string;
  started_at: string | null;
  completed_at: string | null;
  statuses: any[];
}

type FilterMode = "process" | "steps" | "new" | "datapoints";

export default function ReportPage() {
  const navigate = useNavigate();
  const [filterMode, setFilterMode] = useState<FilterMode>("datapoints");
  const [processes, setProcesses] = useState<ProcessWithDetails[]>([]);
  const [stepStatuses, setStepStatuses] = useState<
    Record<string, Record<string, StatusRecord>>
  >({});
  const [expandedProcs, setExpandedProcs] = useState<Record<string, boolean>>(
    {}
  );
  const [expandedSubs, setExpandedSubs] = useState<Record<string, boolean>>(
    {}
  );
  const [selectedField, setSelectedField] = useState<Record<string, string>>(
    {}
  );
  const [loading, setLoading] = useState(true);
  const [sortOrder, setSortOrder] = useState<"asc" | "desc">("desc");
  const cardRefs = useRef<Record<string, RefObject<HTMLDivElement | null>>>(
    {}
  );

  useEffect(() => {
    loadProcessesAndStatuses();
  }, []);

  // sort processes by created_at
  const sortedProcs = [...processes].sort((a, b) => {
    const da = new Date(a.created_at).getTime();
    const db = new Date(b.created_at).getTime();
    return sortOrder === "asc" ? da - db : db - da;
  });

  const formatDuration = (start: string | null, end: string | null): string => {
    if (!start) return "";
    const startDate = new Date(start);
    const endDate = end ? new Date(end) : new Date();
    const diffMs = endDate.getTime() - startDate.getTime();
    if (diffMs < 0) return "";
    const totalMinutes = Math.floor(diffMs / 60000);
    const hours = Math.floor(totalMinutes / 60);
    const minutes = totalMinutes % 60;
    return `${hours}h ${minutes}m`;
  };

  const toMinutes = (value: number = 0, unit: string = "hrs"): number => {
    switch (unit.toLowerCase()) {
      case "min":
      case "mins":
      case "minutes":
        return value;
      case "hrs":
      case "hours":
        return value * 60;
      case "day":
      case "days":
      case "d":
        return value * 1440;
      default:
        return value;
    }
  };

  const formatTotalDuration = (totalMinutes: number): string => {
    if (totalMinutes >= 1440) return `${(totalMinutes / 1440).toFixed(1)} d`;
    if (totalMinutes >= 60) return `${(totalMinutes / 60).toFixed(1)} h`;
    return `${totalMinutes} m`;
  };

  async function loadProcessesAndStatuses() {
    setLoading(true);
    const {
      data: { session },
    } = await supabase.auth.getSession();
    if (!session?.user) {
      setLoading(false);
      return;
    }

    const {
      data: procsData,
      error: procErr,
    } = await supabase
      .from("processes")
      .select(
        `
        id,
        name,
        created_at,
        steps ( id, name, created_at, tat_value, tat_unit, responsibility ),
        form_submissions ( id, data )
      `
      )
      .eq("user_id", session.user.id)
      .order("created_at", { ascending: false });

    if (procErr || !procsData) {
      console.error(procErr);
      setLoading(false);
      return;
    }

    const refs: Record<string, RefObject<HTMLDivElement | null>> = {};
    procsData.forEach((p) => {
      p.steps.sort(
        (a, b) =>
          new Date(a.created_at).getTime() -
          new Date(b.created_at).getTime()
      );
      refs[p.id] = createRef();
    });
    cardRefs.current = refs;
    setProcesses(procsData);

    const submissionIds = procsData.flatMap((p) =>
      p.form_submissions.map((s) => s.id)
    );
    if (!submissionIds.length) {
      setLoading(false);
      return;
    }

    const {
      data: statusesData,
      error: statusErr,
    } = await supabase
      .from("submission_step_statuses")
      .select("submission_id,step_id,started_at,completed_at,statuses")
      .in("submission_id", submissionIds);

    if (statusErr || !statusesData) {
      console.error(statusErr);
      setLoading(false);
      return;
    }

    const map: Record<string, Record<string, StatusRecord>> = {};
    statusesData.forEach((r) => {
      map[r.submission_id] ||= {};
      map[r.submission_id][r.step_id] = r;
    });
    setStepStatuses(map);
    setLoading(false);
  }

  const toggleProc = (id: string) =>
    setExpandedProcs((prev) => ({ ...prev, [id]: !prev[id] }));
  const toggleSub = (id: string) =>
    setExpandedSubs((prev) => ({ ...prev, [id]: !prev[id] }));
  const handleFieldChange = (pid: string, f: string) =>
    setSelectedField((prev) => ({ ...prev, [pid]: f }));

  const renderDataPointsView = () => (
    <>
      {/* Sort control */}
      <div style={styles.sortContainer}>
        <button
          style={sortOrder === "asc" ? styles.sortButtonActive : styles.sortButton}
          onClick={() => setSortOrder("asc")}
        >
          Oldest first
        </button>
        <button
          style={sortOrder === "desc" ? styles.sortButtonActive : styles.sortButton}
          onClick={() => setSortOrder("desc")}
        >
          Newest first
        </button>
      </div>

      {/* Timeline */}
      <div style={styles.timelineContainer}>
        {sortedProcs.map((proc) => {
          const numSteps = proc.steps.length;
          const numDataPts = proc.form_submissions.length;
          const totalMins = proc.steps.reduce(
            (sum, st) => sum + toMinutes(st.tat_value || 0, st.tat_unit || "hrs"),
            0
          );
          const formattedTotal = formatTotalDuration(totalMins);
          const fields = Object.keys(proc.form_submissions[0]?.data || {});
          const isProcOpen = !!expandedProcs[proc.id];

          return (
            <div
              key={proc.id}
              ref={cardRefs.current[proc.id]}
              style={styles.timelineItem}
            >
              <div style={styles.timelineMarker} />
              <div style={styles.timeLabel}>
                {new Date(proc.created_at).toLocaleString(undefined, {
                  year: "numeric",
                  month: "short",
                  day: "numeric",
                  hour: "2-digit",
                  minute: "2-digit",
                })}
              </div>

              <div style={styles.card}>
                {/* Header with badges */}
                <div style={styles.cardHeader}>
                  <div
                    onClick={() => toggleProc(proc.id)}
                    style={{
                      display: "flex",
                      alignItems: "center",
                      cursor: "pointer",
                    }}
                  >
                    {isProcOpen ? (
                      <ChevronDown style={{ marginRight: 8 }} />
                    ) : (
                      <ChevronRight style={{ marginRight: 8 }} />
                    )}
                    <h3 style={styles.processTitle}>{proc.name}</h3>
                  </div>
                  <div style={styles.statsContainer}>
                    <span style={styles.badgeSteps}>{numSteps} Steps</span>
                    <span style={styles.badgeData}>{numDataPts} Data pts</span>
                    <span style={styles.badgeDuration}>{formattedTotal}</span>
                  </div>
                </div>

                {isProcOpen && (
                  <>
                    {fields.length > 0 && (
                      <div style={{ padding: "0 16px 12px" }}>
                        <select
                          value={selectedField[proc.id] || ""}
                          onChange={(e) =>
                            handleFieldChange(proc.id, e.target.value)
                          }
                          style={styles.select}
                        >
                          <option value="">Filter field</option>
                          {fields.map((f) => (
                            <option key={f} value={f}>
                              {f}
                            </option>
                          ))}
                        </select>
                      </div>
                    )}

                    {proc.form_submissions.map((sub) => {
                      const isOpen = !!expandedSubs[sub.id];
                      const stMap = stepStatuses[sub.id] || {};
                      const chosen = selectedField[proc.id];
                      const chosenVal = chosen ? sub.data?.[chosen] : null;

                      // per-data-point progress
                      const completedCount = proc.steps.filter(
                        (st) => !!stMap[st.id]?.completed_at
                      ).length;
                      const pct = Math.round((completedCount / numSteps) * 100);

                      return (
                        <div key={sub.id} style={styles.subSection}>
                          <div
                            style={styles.subHeader}
                            onClick={() => toggleSub(sub.id)}
                          >
                            {isOpen ? <ChevronDown /> : <ChevronRight />}
                            <span style={styles.subTitle}>
                              Data Point: {sub.id}
                              {chosen && (
                                <span style={styles.subBadge}>
                                  {chosen}: {String(chosenVal ?? "")}
                                </span>
                              )}
                            </span>
                          </div>

                          {isOpen && (
                            <div style={styles.subContent}>
                              {/* per-data-point progress bar */}
                              <div style={styles.progressContainer}>
                                <div
                                  style={{
                                    ...styles.progressBar,
                                    width: `${pct}%`,
                                  }}
                                />
                              </div>
                              <div style={styles.progressText}>
                                {completedCount} / {numSteps} steps ({pct}
                                %)
                              </div>

                              <div style={styles.dataGrid}>
                                {Object.entries(sub.data || {}).map(([k, v]) => (
                                  <div key={k} style={styles.dataCell}>
                                    <strong>{k}</strong>
                                    <br />
                                    {String(v)}
                                  </div>
                                ))}
                              </div>

                              {proc.steps.map((step) => {
                                const rec = stMap[step.id] || {};
                                const started = rec.started_at;
                                const completedAt = rec.completed_at;
                                const bgColor = !started
                                  ? (styles.badgePending.background as string)
                                  : !completedAt
                                  ? (styles.badgeInProgress.background as string)
                                  : (styles.badgeCompleted.background as string);
                                const statusText = !started
                                  ? "Pending"
                                  : !completedAt
                                  ? "In progress"
                                  : "Completed";
                                const duration = formatDuration(
                                  started,
                                  completedAt
                                );
                                return (
                                  <div
                                    key={step.id}
                                    style={{
                                      ...styles.stepCard,
                                      background: bgColor,
                                    }}
                                  >
                                    <div style={styles.stepHeader}>
                                      <strong>{step.name}</strong>
                                      <span
                                        style={
                                          !started
                                            ? styles.badgePending
                                            : !completedAt
                                            ? styles.badgeInProgress
                                            : styles.badgeCompleted
                                        }
                                      >
                                        {statusText}
                                      </span>
                                    </div>
                                    <div style={styles.stepInfo}>
                                      <ClockIcon
                                        style={{ marginRight: 4 }}
                                      />
                                      <span>
                                        {duration}
                                        {!completedAt && " (ongoing)"}
                                      </span>
                                      <span style={styles.byText}>
                                        Completed by:{" "}
                                        {completedAt
                                          ? step.responsibility || "Unknown"
                                          : ""}
                                      </span>
                                    </div>
                                  </div>
                                );
                              })}
                            </div>
                          )}
                        </div>
                      );
                    })}
                  </>
                )}
              </div>
            </div>
          );
        })}
      </div>
    </>
  );

  return (
    <div style={styles.container}>
      <h2 style={styles.title}>Report for Process / Data Points / Steps</h2>
      <div style={{ marginBottom: 16 }}>
        <button
          style={filterMode === "process" ? styles.filterActive : styles.filterBtn}
          onClick={() => {
            setFilterMode("process");
            navigate("/report");
          }}
        >
          By Process
        </button>
        <button
          style={filterMode === "steps" ? styles.filterActive : styles.filterBtn}
          onClick={() => {
            setFilterMode("steps");
            navigate("/report");
          }}
        >
          By Step
        </button>
        <button
          style={filterMode === "new" ? styles.filterActive : styles.filterBtn}
          onClick={() => {
            setFilterMode("new");
            navigate("/report/processstepdatapoints");
          }}
        >
          New Report Page
        </button>
        <button
          style={filterMode === "datapoints" ? styles.filterActive : styles.filterBtn}
          onClick={() => {
            setFilterMode("datapoints");
            navigate("/report/datapoints");
          }}
        >
          By Data Points
        </button>
      </div>
      {loading ? <p>Loading</p> : renderDataPointsView()}
    </div>
  );
}

const BLUE = "#007bff";

const styles: Record<string, React.CSSProperties> = {
  filterBtn: {
    padding: "6px 12px",
    marginRight: 8,
    border: "1px solid #ccc",
    background: "#fff",
    cursor: "pointer",
  },
  filterActive: {
    padding: "6px 12px",
    marginRight: 8,
    border: `1px solid ${BLUE}`,
    background: "#E6F4FF",
    cursor: "pointer",
  },
  sortContainer: {
    display: "inline-flex",
    border: `1px solid ${BLUE}`,
    borderRadius: 4,
    overflow: "hidden",
    marginBottom: 16,
  },
  sortButton: {
    padding: "6px 12px",
    border: "none",
    background: "#fff",
    color: BLUE,
    cursor: "pointer",
    fontSize: 14,
  },
  sortButtonActive: {
    padding: "6px 12px",
    border: "none",
    background: BLUE,
    color: "#fff",
    cursor: "pointer",
    fontSize: 14,
  },

  container: { maxWidth: 920, margin: "auto", padding: 24 },
  title: { marginBottom: 24, textAlign: "center" },

  timelineContainer: {
    position: "relative",
    paddingLeft: -20,
    borderLeft: `3px solid ${BLUE}`,
  },
  timelineItem: { position: "relative", marginBottom: 32 },
  timelineMarker: {
    position: "absolute",
    left: -11,
    top: 8,
    width: 14,
    height: 14,
    borderRadius: "50%",
    background: BLUE,
    border: "3px solid #fff",
    boxSizing: "content-box",
  },

  timeLabel: {
    position: "absolute",
    left: -120,
    width: 90,
    textAlign: "right",
    fontSize: 12,
    color: "#888",
    top: 4,
  },
  card: {
    background: "#fff",
    borderRadius: 8,
    boxShadow: "0 2px 8px rgba(0,0,0,0.05)",
  },
  cardHeader: {
    display: "flex",
    alignItems: "center",
    padding: 16,
    borderBottom: `1px solid ${BLUE}33`,
  },
  processTitle: { fontSize: 18, margin: 0, color: "#333" },
  statsContainer: {
    marginLeft: "auto",
    display: "flex",
    gap: 8,
    alignItems: "center",
  },
  badgeSteps: {
    background: "#e6f7ff",
    color: BLUE,
    padding: "4px 8px",
    borderRadius: 4,
    fontSize: 12,
  },
  badgeData: {
    background: "#e6ffed",
    color: "#28a745",
    padding: "4px 8px",
    borderRadius: 4,
    fontSize: 12,
  },
  badgeDuration: {
    background: "#fff5e6",
    color: "#fd7e14",
    padding: "4px 8px",
    borderRadius: 4,
    fontSize: 12,
  },

  select: {
    padding: "6px 12px",
    borderRadius: 4,
    border: `1px solid ${BLUE}66`,
    fontSize: 14,
    width: "100%",
  },
  subSection: { marginTop: 12 },
  subHeader: {
    display: "flex",
    alignItems: "center",
    cursor: "pointer",
    userSelect: "none",
    marginBottom: 8,
    padding: "0 16px",
  },
  subTitle: { marginLeft: 8, fontWeight: 500, color: "#444" },
  subBadge: {
    marginLeft: 12,
    background: `${BLUE}22`,
    color: BLUE,
    padding: "2px 6px",
    borderRadius: 4,
    fontSize: 12,
  },
  subContent: { padding: "0 16px 16px", borderLeft: "2px solid #f0f0f0" },
  dataGrid: {
    display: "grid",
    gridTemplateColumns: "repeat(auto-fill,minmax(120px,1fr))",
    gap: 12,
    marginBottom: 16,
  },
  dataCell: {
    background: "#f9f9f9",
    padding: 8,
    borderRadius: 4,
    fontSize: 13,
    color: "#333",
  },

  progressContainer: {
    height: 8,
    background: "#eee",
    borderRadius: 4,
    margin: "8px 16px",
    overflow: "hidden",
  },
  progressBar: {
    height: "100%",
    background: BLUE,
  },
  progressText: {
    fontSize: 12,
    padding: "0 16px 8px",
  },

  stepCard: {
    borderRadius: 6,
    padding: 12,
    marginBottom: 8,
    boxShadow: "0 1px 4px rgba(0,0,0,0.04)",
  },
  stepHeader: {
    display: "flex",
    justifyContent: "space-between",
    alignItems: "center",
    marginBottom: 6,
  },
  badgePending: {
    background: "#f8d7da",
    color: "#721c24",
    padding: "2px 8px",
    borderRadius: 12,
    fontSize: 12,
  },
  badgeInProgress: {
    background: "#fff3cd",
    color: "#856404",
    padding: "2px 8px",
    borderRadius: 12,
    fontSize: 12,
  },
  badgeCompleted: {
    background: "#d4edda",
    color: "#155724",
    padding: "2px 8px",
    borderRadius: 12,
    fontSize: 12,
  },
  stepInfo: {
    display: "flex",
    alignItems: "center",
    gap: 12,
    fontSize: 13,
    color: "#666",
  },
  byText: { marginLeft: "auto", fontStyle: "italic", fontSize: 12 },
};
