// src/pages/BulkAddFlowchart.tsx
import React, { useEffect, useState } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import { supabase } from '../supabaseClient';

/**
 * Each step row in the UI:
 *  - isNew: whether the row is newly added or existing
 *  - stepId: an integer grouping
 *  - sequenceId: integer order; same => parallel
 *  - stepName, tat, responsibility: user inputs
 */
interface AllStep {
  isNew: boolean;
  stepId: number;
  sequenceId: number;
  stepName: string;     // maps to steps.name
  tat: string;          // maps to steps.tat_value (converted to int)
  responsibility: string;
}

export default function BulkAddFlowchart() {
  const { processId } = useParams(); 
  const navigate = useNavigate();

  const [allSteps, setAllSteps] = useState<AllStep[]>([]);
  const [loading, setLoading] = useState(true);
  // Track which rows three dot menu is open (by index)
  const [openMenuIndex, setOpenMenuIndex] = useState<number | null>(null);

  useEffect(() => {
    if (processId) {
      fetchExistingSteps(processId);
    }
  }, [processId]);

  // 1) Fetch existing steps => read-only
  const fetchExistingSteps = async (pid: string) => {
    setLoading(true);

    const { data, error } = await supabase
      .from('steps')
      .select(`
        step_id,
        sequence_id,
        name,
        tat_value,
        responsibility
      `)
      .eq('process_id', pid)
      .order('sequence_id', { ascending: true });

    if (error) {
      console.error('Error fetching steps:', error);
      setAllSteps([]);
      setLoading(false);
      return;
    }
    if (!data) {
      setAllSteps([]);
      setLoading(false);
      return;
    }

    const existing = data.map((row: any) => ({
      isNew: false,
      stepId: row.step_id || 0,
      sequenceId: row.sequence_id || 0,
      stepName: row.name || '',
      tat: row.tat_value != null ? String(row.tat_value) : '',
      responsibility: row.responsibility || '',
    })) as AllStep[];

    setAllSteps(existing);
    setLoading(false);
  };

  // Instead of adding a step inline, navigate to the add-step screen.
  // For parallel steps, only the stepId increments while sequenceId remains same.
  const handleAddParallel = (index: number) => {
    const current = allSteps[index];
    const newStepId = current.stepId + 1;
    const newSequenceId = current.sequenceId; // remains same for parallel
    navigate(
      `/process/${processId}/add-step?stepId=${newStepId}&sequenceId=${newSequenceId}&mode=parallel`
    );
  };

  // For a next (serial) step, both stepId and sequenceId increase by 1.
  const handleAddBelow = (index: number) => {
    const current = allSteps[index];
    const newStepId = current.stepId + 1;
    const newSequenceId = current.sequenceId + 1;
    navigate(
      `/process/${processId}/add-step?stepId=${newStepId}&sequenceId=${newSequenceId}&mode=below`
    );
  };

  // When editing, navigate to the add-step screen with existing values.
  const handleEditStep = (index: number) => {
    const step = allSteps[index];
    navigate(
      `/process/${processId}/add-step?stepId=${step.stepId}&sequenceId=${step.sequenceId}&mode=edit`
    );
  };

  // Delete a step.
  const handleDeleteStep = async (index: number) => {
    const step = allSteps[index];
    if (!processId) return;

    // If the step is new, simply remove from local state.
    if (step.isNew) {
      const updated = [...allSteps];
      updated.splice(index, 1);
      setAllSteps(updated);
      return;
    }

    // For existing steps, confirm and then delete from DB.
    const confirmDel = window.confirm(`Are you sure you want to delete step "${step.stepName}"?`);
    if (!confirmDel) return;

    const { error } = await supabase
      .from('steps')
      .delete()
      .eq('process_id', processId)
      .eq('step_id', step.stepId)
      .eq('sequence_id', step.sequenceId);
    if (error) {
      alert('Error deleting step: ' + error.message);
      return;
    }
    // Remove the step from local state.
    const updated = [...allSteps];
    updated.splice(index, 1);
    setAllSteps(updated);
  };

  // Toggle three dot menu for the row at index idx.
  const toggleMenu = (idx: number) => {
    setOpenMenuIndex((prev) => (prev === idx ? null : idx));
  };

  // 6) Save new steps if needed (here, only new steps are inserted)
  const handleSaveAll = async () => {
    if (!processId) {
      alert('No processId in URL, cannot save.');
      return;
    }

    // Optionally re-sequence and update new steps
    let final = [...allSteps].sort((a, b) => a.sequenceId - b.sequenceId);
    for (let i = 0; i < final.length; i++) {
      final[i].sequenceId = i + 1;
    }
    setAllSteps(final);

    const newOnes = final.filter((s) => s.isNew);
    for (const ns of newOnes) {
      if (!ns.stepName.trim()) continue;
      const { error } = await supabase
        .from('steps')
        .insert([
          {
            process_id: processId,
            step_id: ns.stepId,
            sequence_id: ns.sequenceId,
            name: ns.stepName,
            tat_value: ns.tat ? parseInt(ns.tat) : null,
            responsibility: ns.responsibility,
          },
        ]);
      if (error) {
        alert('Error inserting step: ' + error.message);
        return;
      }
    }

    alert('All new steps added!');
    navigate(`/process/${processId}/steps`);
  };

  if (loading) {
    return <div style={{ padding: '24px' }}>Loading existing steps...</div>;
  }

  return (
    <div style={{ padding: '24px', fontFamily: 'sans-serif' }}>
      {/* Top bar */}
      <div style={{ display: 'flex', alignItems: 'center', marginBottom: '16px' }}>
        <h2 style={{ margin: 0 }}>Bulk Edit for Process {processId}</h2>
        <div style={{ marginLeft: 'auto' }}>
          <button
            onClick={handleSaveAll}
            style={{
              marginRight: '8px',
              backgroundColor: '#3b82f6',
              color: '#fff',
              padding: '8px 16px',
              borderRadius: '4px',
              border: 'none',
              cursor: 'pointer',
            }}
          >
            Save All
          </button>
          <button
            onClick={() => navigate(-1)}
            style={{
              backgroundColor: '#eee',
              color: '#333',
              padding: '8px 16px',
              borderRadius: '4px',
              border: 'none',
              cursor: 'pointer',
            }}
          >
            Cancel
          </button>
        </div>
      </div>

      {allSteps.map((item, idx) => {
        const { isNew, stepId, sequenceId, tat, stepName, responsibility } = item;
        // Example placeholders for statuses/alerts/reminders
        const statusesCount = 3;
        const alertsCount = 4;
        const remindersCount = 12;

        return (
          <div key={`${stepId}-${sequenceId}-${idx}`} style={{ marginBottom: '16px' }}>
            <div style={{ display: 'flex', alignItems: 'center' }}>
              {/* Step ID / Sequence ID labels */}
              <div
                style={{
                  marginRight: '16px',
                  textAlign: 'center',
                  fontSize: '0.85rem',
                  color: '#888',
                  width: '50px',
                }}
              >
                <div>#{stepId}</div>
                <div>#{sequenceId}</div>
              </div>

              {/* Gray card */}
              <div
                style={{
                  position: 'relative',
                  flex: 1,
                  display: 'flex',
                  alignItems: 'center',
                  justifyContent: 'space-between',
                  border: '1px solid #ccc',
                  borderRadius: '8px',
                  padding: '16px',
                  backgroundColor: '#f5f5f5',
                }}
              >
                {/* Left side: display values (non-editable here) */}
                <div>
                  <div style={{ fontWeight: 'bold', marginBottom: '4px' }}>
                    {tat} hrs
                  </div>
                  <div style={{ marginBottom: '4px' }}>{stepName}</div>
                  <div style={{ fontSize: '0.9rem', color: '#666' }}>
                    Responsibility: {responsibility}
                  </div>
                </div>

                {/* Right side: placeholders for statuses/alerts/reminders */}
                <div style={{ textAlign: 'right', fontSize: '0.85rem', color: '#666' }}>
                  {statusesCount} statuses | {alertsCount} alerts | {remindersCount} reminders
                </div>

                {/* Plus icon for adding a parallel step */}
                <div style={{ marginLeft: '16px' }}>
                  <button
                    onClick={() => handleAddParallel(idx)}
                    style={{
                      backgroundColor: '#eee',
                      border: 'none',
                      borderRadius: '50%',
                      width: '32px',
                      height: '32px',
                      cursor: 'pointer',
                      fontSize: '1.2rem',
                      lineHeight: '32px',
                    }}
                  >
                    +
                  </button>
                </div>

                {/* Three dot menu for Edit/Delete */}
                <div style={{ marginLeft: '16px', position: 'relative' }}>
                  <span
                    style={{
                      cursor: 'pointer',
                      fontSize: '1.2rem',
                      padding: '0 6px',
                    }}
                    onClick={() => toggleMenu(idx)}
                  >
                    
                  </span>
                  {openMenuIndex === idx && (
                    <div
                      style={{
                        position: 'absolute',
                        top: '40px',
                        right: 0,
                        background: '#fff',
                        border: '1px solid #ccc',
                        borderRadius: '4px',
                        zIndex: 2,
                      }}
                    >
                      <div
                        style={{
                          padding: '8px',
                          cursor: 'pointer',
                        }}
                        onClick={() => {
                          handleEditStep(idx);
                          setOpenMenuIndex(null);
                        }}
                      >
                        Edit
                      </div>
                      <div
                        style={{
                          padding: '8px',
                          cursor: 'pointer',
                          color: 'red',
                        }}
                        onClick={() => {
                          handleDeleteStep(idx);
                          setOpenMenuIndex(null);
                        }}
                      >
                        Delete
                      </div>
                    </div>
                  )}
                </div>
              </div>
            </div>

            {/* Plus icon for adding a sequential (below) step */}
            <div style={{ marginLeft: '66px', marginTop: '4px' }}>
              <button
                onClick={() => handleAddBelow(idx)}
                style={{
                  backgroundColor: '#eee',
                  border: 'none',
                  borderRadius: '50%',
                  width: '32px',
                  height: '32px',
                  cursor: 'pointer',
                  fontSize: '1.2rem',
                  lineHeight: '32px',
                }}
              >
                +
              </button>
            </div>
          </div>
        );
      })}
    </div>
  );
}
