import React, { useState, KeyboardEvent } from 'react';
import { useNavigate, To } from 'react-router-dom';
import { supabase } from '../supabaseClient';

interface Field {
  label: string;
  inputType: string;
  options: string;
}

/**
 * FormMaker allows the user to define a new custom form:
 *  - a form name
 *  - any number of fields (label, type, options)
 *  - a submitaction mapping (next / stop / split)
 */
export default function FormMaker() {
  const navigate = useNavigate();

  const [name, setName] = useState<string>('');
  const [fields, setFields] = useState<Field[]>([
    { label: '', inputType: 'text', options: '' },
  ]);
  const [submitAction, setSubmitAction] = useState<'next' | 'stop' | 'split' | ''>('');

  const handleAddField = () => {
    setFields(f => [...f, { label: '', inputType: 'text', options: '' }]);
  };

  const handleFieldKeyDown = (e: KeyboardEvent<HTMLInputElement>, idx: number) => {
    if (e.key === 'Enter' && fields[idx].label.trim() !== '') {
      handleAddField();
    }
  };

  const handleSave = async () => {
    // 1) Ensure user is signed in
    const {
      data: { session },
    } = await supabase.auth.getSession();
    if (!session?.user) {
      alert('Please sign in to create a form.');
      return;
    }

    // 2) Insert new form into `step_forms`
    const { data: form, error: formError } = await supabase
      .from('step_forms')
      .insert({
        name,
        user_id: session.user.id,
        submit_action_mapping: submitAction || null,
      })
      .select('id')
      .single();

    if (formError || !form) {
      alert(`Error creating form: ${formError?.message}`);
      return;
    }

    // 3) Insert each field into `step_form_fields`
    const toInsert = fields.map((f, i) => ({
      form_id: form.id,
      label: f.label,
      input_type: f.inputType,
      options: f.options,
      position: i,
    }));
    const { error: fieldsError } = await supabase
      .from('step_form_fields')
      .insert(toInsert);

    if (fieldsError) {
      alert(`Error saving fields: ${fieldsError.message}`);
      return;
    }

    alert('Form saved successfully!');

    // 4) Navigate back to AddStep, passing the new formId in state.
    //    We cast -1 to `To` so TS picks the overload that accepts state.
    navigate((-1 as unknown) as To, { state: { newFormId: form.id } });
  };

  return (
    <div style={{ maxWidth: 600, margin: 'auto', padding: '1rem', fontFamily: 'Arial, sans-serif' }}>
      <h2>Create New Form</h2>

      <label style={{ display: 'block', margin: '0.5rem 0 0.25rem' }}>Form Name</label>
      <input
        type="text"
        value={name}
        onChange={e => setName(e.target.value)}
        placeholder="Enter form name"
        style={{ width: '100%', padding: '0.5rem', boxSizing: 'border-box' }}
      />

      <h3 style={{ marginTop: '1rem' }}>Fields</h3>
      {fields.map((f, idx) => (
        <div
          key={idx}
          style={{ marginBottom: '1rem', paddingBottom: '0.5rem', borderBottom: '1px solid #eee' }}
        >
          <label style={{ display: 'block', margin: '0.25rem 0' }}>Label</label>
          <input
            type="text"
            value={f.label}
            onChange={e => {
              const copy = [...fields];
              copy[idx].label = e.target.value;
              setFields(copy);
            }}
            onKeyDown={e => handleFieldKeyDown(e, idx)}
            placeholder="Enter field label"
            style={{ width: '100%', padding: '0.4rem', boxSizing: 'border-box' }}
          />

          <label style={{ display: 'block', margin: '0.25rem 0' }}>Input Type</label>
          <select
            value={f.inputType}
            onChange={e => {
              const copy = [...fields];
              copy[idx].inputType = e.target.value;
              setFields(copy);
            }}
            style={{ width: '100%', padding: '0.4rem', boxSizing: 'border-box' }}
          >
            <option value="text">Text</option>
            <option value="dropdown">Dropdown</option>
            <option value="checkbox">Checkbox</option>
          </select>

          {(f.inputType === 'dropdown' || f.inputType === 'checkbox') && (
            <>
              <label style={{ display: 'block', margin: '0.25rem 0' }}>Options (commaseparated)</label>
              <input
                type="text"
                value={f.options}
                onChange={e => {
                  const copy = [...fields];
                  copy[idx].options = e.target.value;
                  setFields(copy);
                }}
                placeholder="e.g. Apple,Banana,Cherry"
                style={{ width: '100%', padding: '0.4rem', boxSizing: 'border-box' }}
              />
            </>
          )}
        </div>
      ))}

      <button
        onClick={handleAddField}
        style={{ padding: '0.4rem 0.8rem', marginBottom: '1rem', cursor: 'pointer' }}
      >
        + Add Field
      </button>

      <div style={{ borderTop: '1px solid #ccc', paddingTop: '1rem', marginTop: '1rem' }}>
        <label style={{ display: 'block', margin: '0.25rem 0' }}>On Submit: Action Mapping</label>
        <select
          value={submitAction}
          onChange={e => setSubmitAction(e.target.value as any)}
          style={{ width: '100%', padding: '0.4rem', boxSizing: 'border-box' }}
        >
          <option value="">-- Select an action --</option>
          <option value="next">Next Step</option>
          <option value="stop">Stop Here</option>
          <option value="split">Split/Progress</option>
        </select>
      </div>

      <button
        onClick={handleSave}
        style={{
          marginTop: '1.5rem',
          padding: '0.6rem 1.2rem',
          background: '#333',
          color: '#fff',
          border: 'none',
          borderRadius: '4px',
          cursor: 'pointer',
        }}
      >
        Save Form
      </button>
    </div>
  );
}
