// src/pages/LoginScreen.tsx
import React, { useEffect, useState } from 'react';
import { supabase } from '../supabaseClient';

interface Process {
  id: string;
  name: string;
  // Add additional fields as needed.
}

const LoginScreen: React.FC = () => {
  const [user, setUser] = useState<any>(null);
  const [processList, setProcessList] = useState<Process[]>([]);
  const [loading, setLoading] = useState<boolean>(false);

  // Listen to authentication state changes.
  useEffect(() => {
    // Fetch current session (if any) when component mounts.
    supabase.auth.getSession().then(({ data: { session } }) => {
      setUser(session?.user || null);
    });

    // Set up a listener for auth state changes.
    const { data: authListener } = supabase.auth.onAuthStateChange((event, session) => {
      setUser(session?.user || null);
    });

    return () => {
      authListener?.subscription.unsubscribe();
    };
  }, []);

  // Trigger sign-in using Supabases OAuth with Google.
  const handleSignIn = async () => {
    const { error } = await supabase.auth.signInWithOAuth({
      provider: 'google',
      // Note: We're no longer using a custom redirectTo value.
      // Ensure that your Supabase dashboard is configured with the correct redirect URL,
      // and that this URL is registered in your Google Developer Console.
    });
    if (error) {
      console.error('Error signing in:', error.message);
    }
  };

  // Upsert the user details into your Supabase "users" table.
  const saveUserData = async (user: any) => {
    const { error } = await supabase.from('users').upsert({
      id: user.id,
      email: user.email,
      // Add additional fields if necessary.
    });
    if (error) {
      console.error('Error saving user data:', error.message);
    }
  };

  // Fetch the process list for the authenticated user.
  const fetchProcessList = async (userId: string) => {
    setLoading(true);
    const { data, error } = await supabase
      .from('processes')
      .select('*')
      .eq('user_id', userId);
    if (error) {
      console.error('Error fetching processes:', error.message);
    } else {
      setProcessList(data);
    }
    setLoading(false);
  };

  // When the user is set, save their data and fetch the process list.
  useEffect(() => {
    if (user) {
      saveUserData(user);
      fetchProcessList(user.id);
    }
  }, [user]);

  return (
    <div style={containerStyle}>
      {user ? (
        <>
          <h1>Welcome, {user.email}</h1>
          <h2>Your Process List</h2>
          {loading ? (
            <p>Loading processes...</p>
          ) : processList.length > 0 ? (
            <ul>
              {processList.map((process) => (
                <li key={process.id}>{process.name}</li>
              ))}
            </ul>
          ) : (
            <p>No processes found.</p>
          )}
        </>
      ) : (
        <>
          <h1>Login</h1>
          <button onClick={handleSignIn} style={buttonStyle}>
            Sign in with Google
          </button>
        </>
      )}
    </div>
  );
};

const containerStyle: React.CSSProperties = {
  display: 'flex',
  flexDirection: 'column',
  alignItems: 'center',
  justifyContent: 'center',
  height: '100vh',
  background: '#f4f4f4',
};

const buttonStyle: React.CSSProperties = {
  background: '#fff',
  color: '#333',
  border: 'none',
  padding: '0.75rem 1.5rem',
  cursor: 'pointer',
  borderRadius: '4px',
  fontSize: '1rem',
};

export default LoginScreen;
