// src/utils/getCaretPosition.ts

/**
 * Very lightly adapted from https://github.com/component/textarea-caret-position
 * Returns { top, left } coordinates of the caret in the textarea (relative to its padding box).
 */
export default function getCaretPosition(element: HTMLTextAreaElement, position: number) {
  const div = document.createElement('div');
  const style = getComputedStyle(element);
  for (const prop of [
    'boxSizing','width','height','overflowX','overflowY',
    'borderTopWidth','borderRightWidth','borderBottomWidth','borderLeftWidth',
    'paddingTop','paddingRight','paddingBottom','paddingLeft',
    'fontStyle','fontVariant','fontWeight','fontStretch','fontSize','lineHeight','fontFamily',
    'textAlign','textTransform','textIndent','whiteSpace','wordWrap'
  ] as const) {
    // @ts-ignore
    div.style[prop] = style[prop];
  }

  div.style.position = 'absolute';
  div.style.visibility = 'hidden';
  div.style.whiteSpace = 'pre-wrap';
  div.style.wordWrap   = 'break-word';

  // Copy the text and insert a span at the caret
  const textBefore = element.value.substring(0, position);
  const textAfter  = element.value.substring(position) || '.';
  div.textContent = textBefore;
  const span = document.createElement('span');
  span.textContent = textAfter;
  div.appendChild(span);
  document.body.appendChild(div);

  const { offsetTop: top, offsetLeft: left } = span;
  document.body.removeChild(div);
  // adjust for the textareas scroll
  return {
    top: top  - element.scrollTop  + parseInt(style.borderTopWidth),
    left: left - element.scrollLeft + parseInt(style.borderLeftWidth)
  };
}
