// src/components/ReminderSection.tsx
import React from 'react';

export interface ReminderData {
  frequency: string;
  till_when: string;
  inputType: string;
  options: string;
}

interface ReminderSectionProps {
  reminders: ReminderData[];
  setReminders: React.Dispatch<React.SetStateAction<ReminderData[]>>;
}

export const ReminderSection: React.FC<ReminderSectionProps> = ({ reminders, setReminders }) => {
  const labelStyle: React.CSSProperties = { display: 'block', marginBottom: '0.25rem', fontWeight: 'bold' };
  const inputStyle: React.CSSProperties = { width: '100%', padding: '0.4rem', boxSizing: 'border-box', marginBottom: '0.5rem' };
  const plusButtonStyle: React.CSSProperties = { cursor: 'pointer', fontWeight: 'bold', fontSize: '1.2rem', background: 'none', border: 'none' };

  const handleAddReminder = () => {
    setReminders(prev => [...prev, { frequency: '', till_when: '', inputType: 'text', options: '' }]);
  };

  const updateReminder = (idx: number, key: keyof ReminderData, value: string) => {
    setReminders(prev => {
      const copy = [...prev];
      copy[idx] = { ...copy[idx], [key]: value };
      return copy;
    });
  };

  return (
    <div style={{ border: '1px solid #ccc', padding: '1rem', marginTop: '1rem', borderRadius: '5px', position: 'relative' }}>
      <h3 style={{ margin: 0, marginBottom: '1rem' }}>Reminders</h3>
      {reminders.map((rem, idx) => (
        <div key={idx} style={{ marginBottom: '1rem', borderBottom: '1px solid #eee', paddingBottom: '0.5rem' }}>
          <label style={labelStyle}>Frequency</label>
          <input
            type="text"
            style={inputStyle}
            value={rem.frequency}
            onChange={e => updateReminder(idx, 'frequency', e.target.value)}
          />
          <label style={labelStyle}>Till When</label>
          <input
            type="text"
            style={inputStyle}
            value={rem.till_when}
            onChange={e => updateReminder(idx, 'till_when', e.target.value)}
          />
          <label style={labelStyle}>Input Type</label>
          <select
            style={inputStyle}
            value={rem.inputType}
            onChange={e => updateReminder(idx, 'inputType', e.target.value)}
          >
            <option value="text">Text</option>
            <option value="checkbox">Checkbox</option>
            <option value="radio">Radio</option>
            <option value="dropdown">Dropdown</option>
            <option value="button">Button</option>
          </select>
          {['checkbox','radio','dropdown'].includes(rem.inputType) && (
            <>
              <label style={labelStyle}>Options (comma-separated)</label>
              <input
                type="text"
                style={inputStyle}
                placeholder="e.g. Daily,Weekly"
                value={rem.options}
                onChange={e => updateReminder(idx, 'options', e.target.value)}
              />
            </>
          )}
        </div>
      ))}
      <button style={plusButtonStyle} onClick={handleAddReminder}></button>
    </div>
  );
};
