import React, { useEffect } from 'react';
import { useNavigate } from 'react-router-dom';

export default function AddForm() {
  const navigate = useNavigate();

  useEffect(() => {
    // 1) open your Netlify app in a new tab
    window.open(
      'https://nimble-phoenix-504247.netlify.app/',
      '_blank',
      'noopener,noreferrer'
    );

    // 2) then prompt the user to paste in their forms URL
    const formUrl = window.prompt(
      'Please paste the URL of your published form here:'
    );

    if (formUrl && formUrl.trim() !== '') {
      // do whatever you need with that URL
      // for example navigate back into your app and include it as state/query
      navigate(`/add-step?formUrl=${encodeURIComponent(formUrl.trim())}`);
    } else {
      // if they cancel or leave it blank, go back
      navigate(-1);
    }
  }, [navigate]);

  return null; 
}
