// src/pages/StepCreation.tsx
import React, { useEffect, useState } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import { supabase } from '../supabaseClient';
import { Step } from '../types';

type ExpandedDetails = {
  [key: string]: {
    statuses?: boolean;
    alerts?: boolean;
    reminders?: boolean;
  };
};

const StepCreation: React.FC = () => {
  const { processId } = useParams(); // e.g. /process/:processId/steps
  const navigate = useNavigate();
  const [steps, setSteps] = useState<Step[]>([]);
  const [loading, setLoading] = useState(true);
  const [formLink, setFormLink] = useState('');
  // State to control which step detail(s) are expanded
  const [expandedDetails, setExpandedDetails] = useState<ExpandedDetails>({});
  // Track index of item being dragged
  const [draggedIndex, setDraggedIndex] = useState<number | null>(null);
  // Track current drop target index (where dragged item is hovering)
  const [dropIndex, setDropIndex] = useState<number | null>(null);

  useEffect(() => {
    if (processId) {
      fetchSteps(processId);
      fetchFormLink(processId);
    }
  }, [processId]);

  // Fetch steps from "steps" table
  const fetchSteps = async (pid: string) => {
    setLoading(true);
    const { data, error } = await supabase
      .from('steps')
      .select('*')
      .eq('process_id', pid)
      .order('sequence_id', { ascending: true })
      .order('created_at', { ascending: true });

    if (error) {
      console.error('Error fetching steps:', error);
      setLoading(false);
      return;
    }
    if (data) {
      setSteps(data);
    }
    setLoading(false);
  };

  // Fetch the form link from "forms" table for this process
  const fetchFormLink = async (pid: string) => {
    const { data, error } = await supabase
      .from('forms')
      .select('google_form_url')
      .eq('process_id', pid)
      .single();

    if (error) {
      console.error('Error fetching form link:', error);
      return;
    }
    if (data?.google_form_url) {
      setFormLink(data.google_form_url);
    }
  };

  // Navigation handlers
  const handleAddStepClick = () => {
    if (!processId) return;
    navigate(`/process/${processId}/add-step`);
  };

  const handleBulkEditClick = () => {
    if (!processId) return;
    navigate(`/process/${processId}/bulk-edit`);
  };

  // Convert TAT value to hours based on its unit
  const convertToHours = (value: number, unit: string): number => {
    switch (unit) {
      case 'mins':
        return value / 60;
      case 'hrs':
        return value;
      case 'days':
        return value * 24;
      case 'weeks':
        return value * 168; // 7 * 24
      default:
        return value;
    }
  };

  // Sum up the total TAT from all steps (converted to hours)
  const totalTat = steps.reduce((acc, step) => {
    return acc + convertToHours(step.tat_value ?? 0, step.tat_unit || 'hrs');
  }, 0);

  // Copy form link to clipboard
  const handleCopyLink = async () => {
    if (!formLink) return;
    try {
      await navigator.clipboard.writeText(formLink);
      alert('Form link copied to clipboard!');
    } catch (err) {
      console.error('Failed to copy:', err);
      alert('Could not copy link.');
    }
  };

  // Toggle expanded detail for a step and type
  const toggleDetail = (
    stepId: string,
    type: 'statuses' | 'alerts' | 'reminders'
  ) => {
    setExpandedDetails((prev) => {
      const current = prev[stepId] || {};
      return {
        ...prev,
        [stepId]: { ...current, [type]: !current[type] },
      };
    });
  };

  // ---------- Drag and Drop Handlers using native events ----------
  const handleDragStart = (e: React.DragEvent<HTMLDivElement>, index: number) => {
    setDraggedIndex(index);
    e.dataTransfer.setData('dragIndex', index.toString());
  };

  const handleDragOver = (e: React.DragEvent<HTMLDivElement>, index: number) => {
    e.preventDefault();
    setDropIndex(index);
  };

  const handleDragLeave = (e: React.DragEvent<HTMLDivElement>) => {
    setDropIndex(null);
  };

  const handleDrop = async (e: React.DragEvent<HTMLDivElement>, dropIndexParam: number) => {
    e.preventDefault();
    const dragIndexStr = e.dataTransfer.getData('dragIndex');
    if (!dragIndexStr) return;
    const dragIndex = parseInt(dragIndexStr, 10);
    if (dragIndex === dropIndexParam) return;

    // Rearranging the steps locally
    const updated = [...steps];
    const [draggedItem] = updated.splice(dragIndex, 1);
    updated.splice(dropIndexParam, 0, draggedItem);

    // Reassign sequence_id and step_id based on new order
    const reSequencedSteps = updated.map((step, index) => ({
      ...step,
      sequence_id: index + 1,
      step_id: index + 1, // reassign step_id as well
    }));

    setSteps(reSequencedSteps);
    setDraggedIndex(null);
    setDropIndex(null);

    // Update the backend for each step (update both sequence_id and step_id)
    for (let step of reSequencedSteps) {
      const { error } = await supabase
        .from('steps')
        .update({ sequence_id: step.sequence_id, step_id: step.step_id })
        .eq('id', step.id);
      if (error) {
        console.error('Failed to update step sequence:', error);
      }
    }
  };

  // Clear draggedIndex on drag end in case drop isn't triggered
  const handleDragEnd = () => {
    setDraggedIndex(null);
    setDropIndex(null);
  };

  if (loading) {
    return <div style={{ padding: '1rem' }}>Loading steps...</div>;
  }

  // ---------------- Inline Styles ----------------
  const containerStyle: React.CSSProperties = {
    maxWidth: '700px',
    margin: 'auto',
    padding: '1rem',
    fontFamily: 'sans-serif',
  };

  const topBarStyle: React.CSSProperties = {
    display: 'flex',
    alignItems: 'center',
    marginBottom: '1rem',
  };

  const backButtonStyle: React.CSSProperties = {
    background: 'none',
    border: 'none',
    fontSize: '1rem',
    cursor: 'pointer',
    marginRight: '0.5rem',
  };

  const filterIconStyle: React.CSSProperties = {
    fontSize: '1.2rem',
    marginRight: '0.5rem',
    cursor: 'pointer',
  };

  const titleStyle: React.CSSProperties = {
    fontSize: '1.2rem',
    fontWeight: 'bold',
  };

  const spacerStyle: React.CSSProperties = {
    flex: 1,
  };

  const totalTatStyle: React.CSSProperties = {
    marginRight: '1rem',
    color: '#555',
  };

  const buttonStyle: React.CSSProperties = {
    backgroundColor: '#eee',
    border: 'none',
    padding: '8px 12px',
    borderRadius: '4px',
    cursor: 'pointer',
    marginLeft: '0.5rem',
  };

  const stepCardStyle: React.CSSProperties = {
    display: 'flex',
    flexDirection: 'column',
    backgroundColor: '#E0E0E0',
    borderRadius: '8px',
    padding: '1rem',
    marginBottom: '0.5rem',
    transition: 'background-color 0.2s ease, border 0.2s ease',
  };

  const stepHeaderStyle: React.CSSProperties = {
    display: 'flex',
    alignItems: 'center',
  };

  const stepLeftStyle: React.CSSProperties = {
    textAlign: 'center',
    fontSize: '0.8rem',
    color: '#666',
    marginRight: '1rem',
    width: '40px',
  };

  const stepMainStyle: React.CSSProperties = {
    flex: 1,
  };

  const topRowStyle: React.CSSProperties = {
    display: 'flex',
    alignItems: 'center',
    marginBottom: '0.25rem',
  };

  const tatStyle: React.CSSProperties = {
    marginRight: '1rem',
    fontWeight: 'bold',
    fontSize: '1rem',
  };

  const stepNameStyle: React.CSSProperties = {
    fontWeight: 'bold',
    fontSize: '1rem',
  };

  const subTextStyle: React.CSSProperties = {
    fontSize: '0.9rem',
    color: '#555',
    marginBottom: '0.25rem',
  };

  const metaStyle: React.CSSProperties = {
    fontSize: '0.8rem',
    color: '#777',
    cursor: 'pointer',
  };

  const detailBoxStyle: React.CSSProperties = {
    backgroundColor: '#fff',
    padding: '0.5rem',
    border: '1px solid #ddd',
    borderRadius: '4px',
    marginTop: '0.5rem',
  };

  // Form link section (if available)
  const formLinkSection = formLink ? (
    <div style={{ marginBottom: '1rem', display: 'flex', alignItems: 'center' }}>
      <span style={{ marginRight: '0.5rem' }}>Form Link:</span>
      <a
        href={formLink}
        target="_blank"
        rel="noreferrer"
        style={{ color: 'blue', textDecoration: 'underline', marginRight: '0.5rem' }}
      >
        {formLink}
      </a>
      <button style={buttonStyle} onClick={handleCopyLink}>
        Copy
      </button>
    </div>
  ) : null;

  return (
    <div style={containerStyle}>
      {formLinkSection}

      {/* Top Bar */}
      <div style={topBarStyle}>
        <button onClick={() => navigate(-1)} style={backButtonStyle}>
          
        </button>
        <span style={filterIconStyle}></span>
        <span style={titleStyle}>Steps for Process {processId}</span>
        <div style={spacerStyle} />
        <div style={totalTatStyle}>{totalTat.toFixed(2)} hrs in total</div>
        <button style={buttonStyle} onClick={handleAddStepClick}>
          + Add a Step
        </button>
        <button style={buttonStyle} onClick={handleBulkEditClick}>
          Bulk Add / Edit
        </button>
      </div>

      {/* Steps List with native drag and drop */}
{/* Steps List with native drag and drop */}
{steps.map((step, index) => {
  // Compute display numbers
  const displayStepId = step.step_id ?? index + 1;
  const displaySeqId = step.sequence_id ?? 0;

  // Style for the card itself (changing on drag/drop)
  const cardStyle = {
    ...stepCardStyle,
    backgroundColor: draggedIndex === index ? '#c8e6c9' : '#E0E0E0',
    borderTop: dropIndex === index ? '3px dashed #388e3c' : 'none',
    flex: 1,
  };

  return (
    <div
      key={step.id}
      style={{
        display: 'flex',
        alignItems: 'stretch',
        marginBottom: '0.5rem',
      }}
    >
      {/*  Left badge rail */}
      <div
        style={{
          width: '40px',
          display: 'flex',
          flexDirection: 'column',
          justifyContent: 'center',
          alignItems: 'center',
          fontSize: '0.8rem',
          color: '#666',
          marginRight: '-8px', // tuck into the card
          zIndex: 1,
          background: 'transparent',
        }}
      >
        <div>#{displayStepId}</div>
        <div>#{displaySeqId}</div>
      </div>

      {/*  The draggable card */}
      <div
        style={cardStyle}
        draggable
        onDragStart={(e) => handleDragStart(e, index)}
        onDragOver={(e) => handleDragOver(e, index)}
        onDragLeave={handleDragLeave}
        onDrop={(e) => handleDrop(e, index)}
        onDragEnd={handleDragEnd}
      >
        <div style={stepHeaderStyle}>
          <div style={{ cursor: 'grab', marginRight: '1rem' }}></div>
          <div style={stepMainStyle}>
            <div style={topRowStyle}>
              <span style={tatStyle}>
                {step.tat_value ?? 0} {step.tat_unit || 'hrs'}
              </span>
              <span style={stepNameStyle}>
                {step.name || 'Name of Step'}
              </span>
            </div>
            <div style={subTextStyle}>
              {(step as any).responsibilities
                ? (step as any).responsibilities.join(', ')
                : step.responsibility || 'Responsibility or a role'}
            </div>
            <div style={metaStyle}>
              <span onClick={() => toggleDetail(step.id, 'statuses')}>
                {(step as any).statuses?.length ?? 0} statuses
              </span>{' '}
              |{' '}
              <span onClick={() => toggleDetail(step.id, 'alerts')}>
                {(step as any).alerts?.length ?? 0} alerts
              </span>{' '}
              |{' '}
              <span onClick={() => toggleDetail(step.id, 'reminders')}>
                {(step as any).reminders?.length ?? 0} reminders
              </span>
            </div>
          </div>
        </div>

        {/* Expanded details */}
        {expandedDetails[step.id]?.statuses && (
          <div style={detailBoxStyle}>
            <strong>Statuses:</strong>
            {(step as any).statuses?.length ? (
              (step as any).statuses.map((st: any, i: number) => (
                <div key={i} style={{ marginBottom: '0.5rem' }}>
                  <strong>{st.statusName}</strong> - {st.statusType}
                  {st.linkNext && <em> (Next: {st.linkNext})</em>}
                </div>
              ))
            ) : (
              <div>No statuses available.</div>
            )}
          </div>
        )}
        {expandedDetails[step.id]?.alerts && (
          <div style={detailBoxStyle}>
            <strong>Alerts:</strong>
            {(step as any).alerts?.length ? (
              (step as any).alerts.map((al: any, i: number) => (
                <div key={i} style={{ marginBottom: '0.5rem' }}>
                  <strong>{al.alert_type}</strong> via {al.alert_via} for{' '}
                  {al.person_or_team}
                </div>
              ))
            ) : (
              <div>No alerts available.</div>
            )}
          </div>
        )}
        {expandedDetails[step.id]?.reminders && (
          <div style={detailBoxStyle}>
            <strong>Reminders:</strong>
            {(step as any).reminders?.length ? (
              (step as any).reminders.map((rem: any, i: number) => (
                <div key={i} style={{ marginBottom: '0.5rem' }}>
                  <strong>{rem.frequency}</strong> - {rem.till_when}
                </div>
              ))
            ) : (
              <div>No reminders available.</div>
            )}
          </div>
        )}
      </div>
    </div>
  );
})}

    </div>
  );
};

export default StepCreation;
