// src/App.tsx
import React, { useEffect, useState } from 'react';
import { BrowserRouter, Routes, Route, Navigate, useNavigate } from 'react-router-dom';
import AddProcess from './pages/AddProcess';
import StepCreation from './pages/StepCreation';
import FlowchartsList from './pages/ProcessList';
import SyncResponses from './pages/SyncResponses';
import AddStep from './pages/AddStep';
import BulkAddFlowchart from './pages/BulkEditSteps';
import DoerStepList from './pages/doer/DoerStepList';
import DoerStepDetail from './pages/doer/DoerStepDetail';
import DoerSubmissionDetail from './pages/doer/DoerSubmissionDetail';
import Navbar from './components/Navbar';
import AuthScreen from './pages/AuthScreen';
import { supabase } from './supabaseClient';
import AddForm from './pages/AddForm';
import FormMaker from './pages/FormMaker';
import ReportPage from './pages/ReportPage'
import ReportProcessStepDataPoints from './pages/ReportProcessStepDataPoints'
import ReportDataPoints from './pages/ReportDataPoints'
import DoerReportPage  from './pages/ReportPageDoer'
import AddTask from './pages/AddTask'
import RandomTaskList from './pages/RandomTaskList'


// A ProtectedRoute component that waits for session retrieval
const ProtectedRoute: React.FC<{ children: React.ReactNode }> = ({ children }) => {
  const [session, setSession] = useState<any>(null);
  const [loading, setLoading] = useState<boolean>(true);

  useEffect(() => {
    // Asynchronously fetch the current session
    supabase.auth.getSession().then(({ data: { session } }) => {
      setSession(session);
      setLoading(false);
    });
  }, []);

  if (loading) return <div>Loading...</div>;
  if (!session) {
    // If there's no session, redirect to login
    return <Navigate to="/login" replace />;
  }
  return <>{children}</>;
};

// Optionally, update your AuthScreen so that if a session already exists, it redirects to the main app.
const UpdatedAuthScreen: React.FC = () => {
  const navigate = useNavigate();

  useEffect(() => {
    supabase.auth.getSession().then(({ data: { session } }) => {
      if (session) {
        navigate('/', { replace: true });
      }
    });
  }, [navigate]);

  return <AuthScreen />;
};

const App: React.FC = () => {
  return (
    <BrowserRouter>
      <Routes>
        {/* Public route for authentication */}
        <Route path="/login" element={<UpdatedAuthScreen />} />

        {/* Protected routes */}
        <Route
          path="/*"
          element={
            <ProtectedRoute>
              <>
                <Navbar />
                <Routes>
                  <Route path="/" element={<FlowchartsList />} />
                  <Route path="/add-process" element={<AddProcess />} />
                  <Route path="/process/:processId/steps" element={<StepCreation />} />
                  <Route path="/process/:flowchartId/add-step" element={<AddStep />} />
                  <Route path="/add-task" element={<AddTask />} />
                  <Route path="/process/:processId/bulk-edit" element={<BulkAddFlowchart />} />
                  <Route path="/doer" element={<DoerStepList />} />
                  <Route path="/doer/step/:stepId/submission/:submissionId" element={<DoerStepDetail />} />
                  <Route path="/sync" element={<SyncResponses />} />
                  <Route path="/add-step/:flowchartId" element={<AddStep />} />
                  <Route path="/add-form" element={<AddForm />} />
                  <Route path="/create-form" element={<FormMaker />} />
                  <Route path="/report" element={<ReportPage />} />
                  <Route path="/report/processstepdatapoints" element={<ReportProcessStepDataPoints />} />
                  <Route path="/report/datapoints" element={<ReportDataPoints />} />
                  <Route path="/report/doer" element={<DoerReportPage />} />
                  <Route path="/random-task-list" element={<RandomTaskList />} />

                </Routes>
              </>
            </ProtectedRoute>
          }
        />
      </Routes>
    </BrowserRouter>
  );
};

export default App;
