// src/pages/DoerReportPage.tsx
import React, { useEffect, useState } from 'react';
import { supabase } from '../supabaseClient';
import { FiChevronDown, FiChevronUp, FiSearch, FiArrowLeft } from 'react-icons/fi';
import { useNavigate } from 'react-router-dom';
import type { IconBaseProps } from 'react-icons';

const ChevronDown = FiChevronDown as React.ComponentType<IconBaseProps>;
const ChevronUp   = FiChevronUp   as React.ComponentType<IconBaseProps>;
const SearchIcon  = FiSearch      as React.ComponentType<IconBaseProps>;
const ArrowLeft   = FiArrowLeft   as React.ComponentType<IconBaseProps>;

interface StatusRecord {
  submission_id: string;
  step_id: string;
  started_at: string | null;
  completed_at: string | null;
}

interface Submission {
  id: string;
  data: Record<string, any>;
  created_at: string;
  statuses?: StatusRecord[];
}

interface Step {
  id: string;
  name: string;
  responsibility: string | null;
}

interface ProcessWithDetails {
  id: string;
  name: string;
  steps: Step[];
  form_submissions: Submission[];
}

export default function DoerReportPage() {
  const navigate = useNavigate();
  const [loading, setLoading] = useState(true);
  const [byDoer, setByDoer] = useState<Record<string, ProcessWithDetails[]>>({});
  const [expandedDoers, setExpandedDoers] = useState<Set<string>>(new Set());
  const [expandedProcs, setExpandedProcs] = useState<Set<string>>(new Set());
  const [expandedSubs, setExpandedSubs] = useState<Set<string>>(new Set());
  const [searchTerm, setSearchTerm] = useState('');

  useEffect(() => {
    loadData();
  }, []);

  async function loadData() {
    setLoading(true);

    const {
      data: { session },
    } = await supabase.auth.getSession();
    if (!session?.user) {
      setLoading(false);
      return;
    }

    const { data: procs } = await supabase
      .from('processes')
      .select(`
        id,
        name,
        steps(
          id,name,responsibility
        ),
        form_submissions(
          id,data,created_at
        )
      `)
      .eq('user_id', session.user.id)
      .order('created_at', { foreignTable: 'steps', ascending: true });

    if (!procs) {
      setLoading(false);
      return;
    }

    const allSubIds = procs.flatMap((p: any) =>
      p.form_submissions.map((s: any) => s.id)
    );
    const statusMap: Record<string, StatusRecord[]> = {};
    if (allSubIds.length) {
      const { data: stats } = await supabase
        .from('submission_step_statuses')
        .select('submission_id,step_id,started_at,completed_at')
        .in('submission_id', allSubIds);
      stats?.forEach((r) => {
        statusMap[r.submission_id] = statusMap[r.submission_id] || [];
        statusMap[r.submission_id].push(r);
      });
    }

    procs.forEach((p: any) => {
      p.form_submissions.forEach((s: any) => {
        s.statuses = statusMap[s.id] || [];
      });
    });

    const map: Record<string, ProcessWithDetails[]> = {};
    procs.forEach((p: ProcessWithDetails) => {
      p.steps.forEach((st: Step) => {
        const who = st.responsibility || 'Unassigned';
        if (!map[who]) map[who] = [];
        if (!map[who].some((x) => x.id === p.id)) {
          map[who].push(p);
        }
      });
    });

    setByDoer(map);
    setLoading(false);
  }

  const toggle = (
    set: Set<string>,
    id: string,
    setter: React.Dispatch<React.SetStateAction<Set<string>>>
  ) => {
    setter((prev) => {
      const nxt = new Set(prev);
      nxt.has(id) ? nxt.delete(id) : nxt.add(id);
      return nxt;
    });
  };

  if (loading) return <p style={styles.loading}>Loading</p>;

  return (
    <div style={styles.container}>
      <button onClick={() => navigate(-1)} style={styles.backBtn}>
        <ArrowLeft /> Back
      </button>
      <h2 style={styles.title}>By Doer</h2>

      {Object.entries(byDoer).map(([doer, procs]) => {
        const isOpenDoer = expandedDoers.has(doer);
        return (
          <div key={doer} style={styles.section}>
            <div
              onClick={() => toggle(expandedDoers, doer, setExpandedDoers)}
              style={styles.header}
            >
              {isOpenDoer ? <ChevronUp /> : <ChevronDown />}
              <span style={styles.headerText}>
                {doer} ({procs.length} proc)
              </span>
            </div>
            {isOpenDoer && (
              <div style={styles.nested}>
                {procs.map((proc) => {
                  const procKey = `${doer}-${proc.id}`;
                  const isOpenProc = expandedProcs.has(procKey);
                  return (
                    <div key={proc.id} style={styles.section}>
                      <div
                        onClick={() =>
                          toggle(expandedProcs, procKey, setExpandedProcs)
                        }
                        style={styles.header}
                      >
                        {isOpenProc ? <ChevronUp /> : <ChevronDown />}
                        <span style={styles.headerText}>
                          {proc.name} ({proc.form_submissions.length} pts)
                        </span>
                      </div>
                      {isOpenProc && (
                        <div style={styles.nested}>
                          <div style={styles.searchRow}>
                            <SearchIcon />
                            <input
                              placeholder="Search data points..."
                              value={searchTerm}
                              onChange={(e) => setSearchTerm(e.target.value)}
                              style={styles.searchInput}
                            />
                          </div>
                          {proc.form_submissions
                            .filter(
                              (sub) =>
                                !searchTerm ||
                                sub.id.includes(searchTerm) ||
                                JSON.stringify(sub.data).includes(
                                  searchTerm
                                )
                            )
                            .map((sub) => {
                              const subKey = `${proc.id}-${sub.id}`;
                              const isOpenSub = expandedSubs.has(subKey);
                              return (
                                <div key={sub.id} style={styles.section}>
                                  <div
                                    onClick={() =>
                                      toggle(
                                        expandedSubs,
                                        subKey,
                                        setExpandedSubs
                                      )
                                    }
                                    style={styles.header}
                                  >
                                    {isOpenSub ? <ChevronUp /> : <ChevronDown />}
                                    <span style={styles.headerText}>
                                      Data Point: {sub.id}
                                    </span>
                                    <span style={styles.meta}>
                                      {new Date(sub.created_at).toLocaleString()}
                                    </span>
                                  </div>
                                  {isOpenSub && (
                                    <>
                                     {/* NEW: Submission Data Table */}
                                      {sub.data && (
                                        <table style={styles.table}>
                                          <thead>
                                            <tr>
                                              <th style={styles.th}>Field</th>
                                              <th style={styles.th}>Value</th>
                                            </tr>
                                          </thead>
                                          <tbody>
                                            {Object.entries(sub.data).map(([key, value]) => (
                                              <tr key={key}>
                                                <td style={styles.td}>{key}</td>
                                                <td style={styles.td}>{String(value)}</td>
                                              </tr>
                                            ))}
                                          </tbody>
                                        </table>
                                      )}
                                      <table style={styles.table}>
                                        <thead>
                                          <tr>
                                            <th style={styles.th}>Step</th>
                                            <th style={styles.th}>Status</th>
                                            <th style={styles.th}>Started</th>
                                            <th style={styles.th}>Completed</th>
                                          </tr>
                                        </thead>
                                        <tbody>
                                          {proc.steps.map((st) => {
                                            const statusRec =
                                              (sub.statuses || []).find(
                                                (r) => r.step_id === st.id
                                              ) || {
                                                started_at: null,
                                                completed_at: null,
                                              };
                                            const status = statusRec.completed_at
                                              ? 'Completed'
                                              : statusRec.started_at
                                              ? 'In Progress'
                                              : 'Pending';
                                            return (
                                              <tr key={st.id}>
                                                <td style={styles.td}>{st.name}</td>
                                                <td style={styles.td}>{status}</td>
                                                <td style={styles.td}>
                                                  {statusRec.started_at ?? ''}
                                                </td>
                                                <td style={styles.td}>
                                                  {statusRec.completed_at ?? ''}
                                                </td>
                                              </tr>
                                            );
                                          })}
                                        </tbody>
                                      </table>

                                     
                                    </>
                                  )}
                                </div>
                              );
                            })}
                        </div>
                      )}
                    </div>
                  );
                })}
              </div>
            )}
          </div>
        );
      })}
    </div>
  );
}

const styles: Record<string, React.CSSProperties> = {
  container: {
    maxWidth: 900,
    margin: 'auto',
    padding: 20,
    fontFamily: 'Arial, sans-serif',
  },
  backBtn: {
    background: 'none',
    border: 'none',
    color: '#1890FF',
    cursor: 'pointer',
    marginBottom: 12,
    display: 'flex',
    alignItems: 'center',
    gap: 4,
  },
  title: { fontSize: 20, marginBottom: 16 },
  loading: { textAlign: 'center', padding: 20 },
  section: {
    marginBottom: 12,
    borderRadius: 4,
    overflow: 'hidden',
  },
  header: {
    display: 'flex',
    alignItems: 'center',
    padding: '8px 12px',
    background: '#f0f0f0',
    cursor: 'pointer',
  },
  headerText: { marginLeft: 8, fontWeight: 500 },
  meta: {
    marginLeft: 'auto',
    fontSize: 12,
    color: '#555',
  },
  nested: { marginLeft: 16, marginTop: 4 },
  searchRow: {
    display: 'flex',
    alignItems: 'center',
    margin: '8px 0',
  },
  searchInput: {
    flex: 1,
    padding: '4px 8px',
    marginLeft: 6,
    borderRadius: 4,
    border: '1px solid #ccc',
  },
  table: {
    width: '100%',
    borderCollapse: 'collapse',
    marginTop: 8,
  },
  th: {
    border: '1px solid #ddd',
    padding: 6,
    background: '#fafafa',
    textAlign: 'left',
    fontSize: 13,
  },
  td: {
    border: '1px solid #ddd',
    padding: 6,
    fontSize: 12,
  },
};
